<template>
    <div>
        <div class="bg-light rounded py-3 px-4 my-2">
            <div class="d-flex justify-content-between align-items-center">
                <div>
                    <span class="fs-6 fw-bold">你的评价会让{{binding==='d'?'乘客':'司机'}}做的更好</span>
                    <span class="rounded p-1 bg-white border ms-2">匿名</span>
                </div>
                <div></div>
            </div>
            <div class="fs-1 text-center d-flex justify-content-around text-black-50">
                <div  v-for="n in 5" :key="n"  class="bd-highlight">
                    <span v-if="show[binding+'evaluate']===0" class="bi bi-star-fill" :class="{'text-warning':n<=star}" @click="onstar(n)"></span>
                    <span v-else class="bi bi-star-fill" :class="{'text-warning':n<=show[binding+'evaluate']}" @click="onstar(n)"></span>
                </div>
            </div>
        </div>
        <EvaluateForm v-if="show[binding+'evaluate']===0" 
        :binding="binding" :star="star" :selected="selected"  
        :starname="starname" :starlabel="starlabel[binding]" 
        @onstar="onstar" @onselect="onselect" @onsubmit="onsubmit" />
        <EvaluateShow v-else v-show="isshow" 
        :binding="binding" :star="show[binding+'evaluate']" 
        :selected="show[binding+'evaluate_label']?show[binding+'evaluate_label'].split(','):[]" 
        :starname="starname" :starlabel="starlabel[binding]" 
        @isActive="isshow=false" />
    </div>
</template>

<script>
import EvaluateForm from "./trip-evaluate-form";
import EvaluateShow from "./trip-evaluate-show";
import axios from 'axios'
export default {
  components: {
    EvaluateForm,
    EvaluateShow,
  },
  props: {
binding:'',
    set:{},
    data:{},
    show:{},
  },
  data(){
      return {
          star:0,
          starname:['未评','非常不满意,各方面都很差','不满意,比较差','一般,还需发送','比较满意,仍可改善','非常满意,无可挑剔'],
          starlabel:{"d":[
              {'1':'有不合理要求','2':'态度较差','3':'沟通不畅','4':'有不文明行为','5':'未按约定上车','6':'聒噪吵闹'},
              {'7':'要求合理','8':'文明守礼','9':'沟通顺畅','10':'素质较高','11':'准时上车','12':'安静乘车'},
          ],"p":[
              {'1':'车况较差','2':'态度较差','3':'道路不熟','4':'临时加价','5':'迟到到达','6':'危险驾驶'},
              {'7':'车内干净','8':'文明守礼','9':'路线熟悉','10':'不多收费用','11':'按约定等候','12':'驾驶技术优秀'},
          ]},
          selected:[],
          isshow:false,
          loading:false,
      }
  },
  methods:{
    onstar(n){
        if(this.show[this.binding+'evaluate']===0){
        this.star=n;
        this.selected=[];
        }else{
        this.isshow=true;
        }
    },
    onselect(key){
        //重新渲染
        if(this.selected.includes(key)){
            this.selected=this.selected.filter(function (ele){return ele != key;});
        }else{
            this.selected.push(key);
        }
    },
    onsubmit(){
         this.$emit('onsubmit',{method: 'post',url:(this.binding==='d'?'/passenger/evaluate':'/driver/evaluate'),params:{token:this.$store.state.token},data:{id:this.show.id,euid:this.show[(this.binding==='d'?'user_id':'duser_id')],evaluate:this.star,evaluate_label:this.selected.join(',')}});
    },
  }
}
</script>



