<template>
  <div class="bg-white">
      <div class="d-flex justify-content-between p-2">
        <div class="bd-highlight fw-bold fs-6">{{set.title}}</div>
        <div class="bd-highlight text-black-50 fs-normal" @click="$tourl(set.url)">{{set.rightname}} 
          <span v-if="set.number"><span class="text-primary" >{{set.number}}</span> {{set.unit}}</span><span class="bi bi-chevron-right"></span></div>
      </div>
      <div class="py-2" v-if="data[set.list.binding]">
        <ViewList v-if="data[set.list.binding].length>0" :set="set.list.set" :data="data[set.list.binding]"/>
      </div>
  </div>
</template>

<script>
import ViewList from '../block/start';
export default {
  components: { 
    ViewList,
  },
  props: {
    set:{},
    data:{},
  },
}
</script>

<style>

</style>


