<template>
 <com-layout :layout_class="set.layout_class" :head_class="set.head_class" :main_class="set.main_class" :foot_class="set.foot_class">
    <template slot="head">
      <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="data" />
    </template>
    <template slot="main" v-if="chatarr">
        <div v-for="(item,key) in chatarr" :key="key">
            <div v-if="item.user_id"  class="d-flex justify-content-between p-2 border-bottom bg-white" @click="$tourl('chat-'+item.user_id)">
                <div>
                    <span class="position-relative d-inline-block">
                      <com-avatar :i="item.avatar" width="50" height="50" class="border border-2 border-white" />
                      <span v-if="item.int>0" class="position-absolute start-100 translate-middle badge rounded-pill bg-danger" style="top:10px;">{{item.int}}</span>
                    </span>
                    <span class="fw-bold">{{item.nickname}}</span>
                </div>
                <div>
                    <div class="text-black-50">{{item.createtime*1000|setdate}}</div>
                </div>
            </div>
        </div>
    </template>
    <template slot="main" v-else>
      <div class="text-center">
          <div><span class="text-black-50 bi bi-emoji-smile" style="font-size: calc(8rem + 1.5vw)!important"></span></div>
          <div class="fs-5 text-black-50">暂无数据</div>
      </div>
    </template>
    <template slot="foot">
      <View-group v-for="(item,key) in set.foot" :key="key" :set="item" :type="item.type" :data="item.data" />
    </template>
  </com-layout>
</template>


<script>
import ViewList from '../block/list';
import LayoutFrame from '../block/layout/frame-1';
import ViewGroup from '../group/start'
export default {
  components: {
    ViewGroup,
    LayoutFrame,
    ViewList,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      userinfo:this.$store.state.userinfo,
      chatarr:this.$store.state.chatarr,
    }
  },
  created(){
    if(this.$store.state.status==='error'){
        this.$tourl('login');
    }else if(this.$store.state.status==='success'){
        this.userinfo=this.$store.state.userinfo;
    }
    this.$root.$on('setuserstate',(msg)=>{ 
        if(msg==='ok'){
        if(this.$store.state.status==='success'){
            this.userinfo=this.$store.state.userinfo;
        }else{
            this.$tourl('login');  
        }
        }else{
        this.$tourl('/');  
        }
    });
    this.$root.$on('setchatarrstate',(msg)=>{ 
        this.chatarr = this.$store.state.chatarr;
    });
  },
  methods:{
      onsubmit(key){
         this.$emit('onsubmit',key);
      },
  }
}
</script>

<style>

</style>


