<template>
    <div class="position-relative" v-if="show.ispc===1">
        <div class="position-absolute start-0 end-0" style="top:-80px">
        <div class="bg-info bg-gradient  rounded-pill shadow-sm py-1 ps-3 pe-2  fs-sm text-white">
            <div class="d-flex justify-content-between align-items-center"  @click="$tourl('dtrip-'+show.id+'-'+show.token)">
              <div class="bd-highlight">
                <div class="fw-bold" style="font-size: 0.5rem!important;">再拼一单</div>
              </div>
              <div class="bd-highlight">
                  <span class="fs-6 bi bi-chevron-right"></span>
              </div>
            </div>
        </div>
        </div>
    </div>
</template>
<script>
export default {
  props: {
    set:{},
    data:{},
    show:{},
  },
}
</script>

