<template>
 <com-layout :layout_class="set.layout_class" :head_class="set.head_class" :main_class="set.main_class" :foot_class="set.foot_class">
    <template slot="head">
      <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="data" />
    </template>
    <template slot="main">
      <View-group v-for="(item,key) in group" :key="key" :set="item" :type="item.type" :data="{dtrip:dtrip,driver:$store.state.userinfo.driver,route:$store.state.userinfo.route}"  @onsubmit="onsubmit"/>
    </template>
    <template slot="foot">
      <View-group v-for="(item,key) in set.foot" :key="key" :set="item" :type="item.type" :data="item.data" />
    </template>
  </com-layout>
</template>


<script>
import ViewGroup from '../group/start'
export default {
  components: {
    ViewGroup,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      dtrip:this.$store.state.userinfo.dtrip,
    }
  },
  created(){
    if(this.$store.state.status==='error'){
        this.$tourl('login');
    }else if(this.$store.state.status==='success'){
        this.dtrip=this.$store.state.userinfo.dtrip;
        if(this.$store.state.userinfo.isdriver!==1){
            this.$tourl('isdriver');
        }
    }
    this.$root.$on('setuserstate',(msg)=>{ 
        if(msg==='ok'){
        if(this.$store.state.status==='success'){
            this.dtrip = Object.assign({});
            this.$todd(this.$store.state.userinfo.dtrip).then(data => {
                this.dtrip = data;
            });
        }else{
            this.$tourl('login');  
        }
        }else{
        this.$tourl('/');  
        }
    });
  },
  methods:{
      onsubmit(key){
         this.$emit('onsubmit',key);
      },
  }
}
</script>

<style>

</style>


