<template>
    <div class="d-grid" v-if="show.state==='1'">
        <button class=" btn btn-primary" @click="$emit('onsubid',{url:'/driver/tongxin',data:{}})">{{data.push[2]?'再拼一单':'确认同行'}}</button>
    </div>
</template>

<script>
export default {
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  }
}
</script>

