import Vue from 'vue'
import Vuex from 'vuex'

import indexJson from '@/json/passenger.json';
import passengerEdit from '@/json/passenger-edit.json';
import driver from '@/json/driver.json';
import chatarr from '@/json/chatarr.json';

import driverEdit from '@/json/driver-edit.json';
import loginJson from '@/json/login.json';
import myuser from '@/json/myuser.json';
import myprofile from '@/json/myprofile.json';
import mynickname from '@/json/mynickname.json';
import mymobile from '@/json/mymobile.json';
import mymoney from '@/json/mymoney.json';
import myset from '@/json/myset.json';

const datacache = {
    index: { cache: 3, name: 'trip', data: indexJson },
    passenger: { cache: 3, name: 'trip', data: indexJson },
    passenger_edit: { cache: 1, data: passengerEdit },
    driver: { cache: 3, name: 'dtrip', data: driver },
    driver_edit: { cache: 1, data: driverEdit },
    chatarr: { cache: 1, data: chatarr },
    myuser: { cache: 1, data: myuser },
    myprofile: { cache: 1, data: myprofile },
    mynickname: { cache: 1, data: mynickname },
    mymobile: { cache: 1, data: mymobile },
    mymoney: { cache: 1, data: mymoney },
    myset: { cache: 1, data: myset },
    login: { cache: 1, data: loginJson },
}
Vue.use(Vuex)
export default new Vuex.Store({
    state: {
        apiset: {
            url: 'http://www.ecgam.com/index.php/api'
        },
        imgurl: 'http://git.ecgam.com',
        webrefresh: 0,
        userinfo: '',
        status: '',
        token: localStorage.getItem('token') || '',
        chatarr: "",
        origin: localStorage.getItem('origin') || '',
        origin_lng: localStorage.getItem('origin_lng') || '',
        origin_lat: localStorage.getItem('origin_lat') || '',
        datacache: datacache,
        datacache_name: '',
    },
    getters: {

    },
    mutations: {
        auth_request(state) {
            state.status = 'loading';
        },
        auth_success(state, key) {
            state.status = 'success';
            state.token = key.token;
            state.userinfo = key;
        },
        auth_error(state) {
            state.status = 'error';
            state.token = '';
            state.userinfo = '';
        },
        auth_userinfo(state, key) {
            state.status = 'success';
            state.userinfo = key;
        },
        auth_userval(state, key) {
            state.userinfo[key[0]] = key[1];
        },
        logout(state) {
            state.status = '';
            state.token = '';
        },
        set_origin(state, data) {
            state.origin = data.name;
            state.origin_lng = data.lng;
            state.origin_lat = data.lat;
        },
        set_webrefresh(state, msg) {
            state.webrefresh = msg;
        },
        set_datacache(state, data) {
            state.datacache[data.name] = data.data;
        },
        set_datacache_name(state, key) {
            state.datacache_name = key ? state.datacache_name.replace('{' + key + '}', '') + '{' + key + '}' : '';
        },
        set_trip(state, key) {
            if (state.userinfo.trip) {
                for (let x in state.userinfo.trip) {
                    if (state.userinfo.trip[x].id === key.id) {
                        state.userinfo.trip[x] = key;
                    }
                }
            }
        },
        set_dtrip(state, key) {
            if (state.userinfo.dtrip) {
                for (let x in state.userinfo.dtrip) {
                    if (state.userinfo.dtrip[x].id === key.id) {
                        state.userinfo.dtrip[x] = key;
                    }
                }
            }
        },
        set_chatarr(state, key) {
            state.chatarr = key;
        },
        set_chatarr_val(state, key) {
            state.chatarr[key[0]] = key[1];
        },
    }
})


