<template>
  <div class="bg-white fs-normal rounded position-relative" @click="$emit('geturl','id',data.id)">
    <div class="d-flex justify-content-between align-items-center p-2">
      <div class="bd-highlight">
        <div><span class="text-primary fw-bold">{{data.state|driverstate}}</span>  <span class="text-black-50">{{data.setdate|setdate(data.setdate_end)}} | {{set.people_name}} {{data.people}}人</span></div>
        <div>
          <div class="ymerge">{{data.origin}}</div>
          <div class="ymerge">{{data.destination}}</div>
        </div>
      </div>
       <div class="bd-highlight"><span class="fs-4 bi bi-chevron-right"></span></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
  },
}
</script>



