<template>
  <div>
    <Viewinput 
    v-for="(item,index) in data" :key="index"
    :name="index" 
    :data="item" 
    :form="form"
    @getform="getform"
    @onsubmit="onsubmit"
    />
  </div>
</template>

<script>
import formdata from './formdata.json';
import Viewinput from './input';
export default {
  components: { 
    Viewinput,
  },
  props: {
    set:{},
    data:{},
    form:{},
  },
  created(){
    this.$emit('getinfo',this.form);
    //设置数据
    for(let x in this.data){
      if(this.data[x].binding){
        for(let e in formdata[this.data[x].binding]){
            if(!this.data[x][e]){
              this.$set(this.data[x],e,formdata[this.data[x].binding][e]);
            }
        }
      }
    }
  },
  methods:{
      getform(){
        this.$emit('getinfo',this.form);
      },
      onsubmit(key){
       this.$emit('onsubmit',key);
      }
  }
}
</script>

<style>

</style>


