<template>
  <div class="bg-white m-2 p-2 rounded shadow-sm">
      <div class="d-flex justify-content-between p-2">
        <div class="bd-highlight fw-bold"><span class="d-inline-block bg-primary rounded-pill bi bi-person-fill text-white"></span>{{set.title}}</div>
        <div class="bd-highlight text-black-50 fs-normal" @click="$tourl(set.url)">{{set.rightname}} 
          <span v-if="set.number"><span class="text-primary" >{{set.number}}</span> {{set.unit}}</span><span class="bi bi-chevron-right"></span></div>
      </div>
      <div class="py-2">
        <ViewList :set="set.set" :data="[{}]"/>
      </div>
  </div>
</template>

<script>
import ViewList from '../block/start';
export default {
  components: { 
    ViewList,
  },
  props: {
    set:{},
    data:{},
  },
  methods:{
      
  }
}
</script>

<style>

</style>


