<template>
  <div>
    <div class="d-flex justify-content-between align-items-center p-2 border border-bottom border-light"  @click="data.isActive=true">
        <div class="bd-highlight fs-6">
            {{data.name}}
        </div>
        <div class="bd-highlight">
            <com-avatar :i="form[name]" width="60" height="60" class="border border-1 border-white"/>
            <span class="bi bi-chevron-right"></span>
        </div>
    </div>
    <LayoutFrom :title="data.title" :text="data.text" :isActive="data.isActive" @isActive="onActive">
        <div class="container-fluid p-2">
        <div class="row row-cols-3 g-2">
            <div class="col text-center" v-for="(item,key) in data.selectdd" :key="key" @click="selected=item">
                <com-avatar :i="item" width="80" height="80" class="border border-3" :class="item===selected?'border-info':'border-white'"/>
            </div>
        </div>
        </div>
        <div class="d-grid p-2">
            <button class=" btn btn-primary" @click="onsubmit">确定</button>
        </div>
    </LayoutFrom>
   </div>  
</template>

<script>
import LayoutFrom from './layout-bottom';
export default {
  components: { 
    LayoutFrom,
  },
  props: {
    name:"",
    data:{},
    form:{},
  },
  data(){
      return {
          selected:'',
      }
  },
  created(){
     this.$set(this,"selected",this.form[this.name]);
  },
  methods:{
      onActive(){
        this.data.isActive=false;
        this.$set(this,"selected",this.form[this.name]);
      },    
      onsubmit(){
        this.form[this.name]=this.selected
        if(this.data.accept){
          let data={};
          data[this.name]=this.selected;
          this.$emit('onsubmit',{method: 'post',url:this.data.accept,params:{token:this.$store.state.token,result:{go:1}},data:data});
        }
        this.$emit('getrefresh',this.name)
        this.$emit('getform',this.name,this.form[this.name]);
        this.onActive();
      },
  }
}
</script>

<style>

</style>


