<template>
 <div class="bg-light">
    <div ref="head">
      <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="data" />
    </div>
    <div ref="main" class="overflow-auto" :style="{height:bodyheight+'px'}">
      <div v-if="loading" class="position-fixed top-0 start-0 end-0 bottom-0 ">
        <div class="position-absolute top-50 start-50 translate-middle"><span class="spinner-border"></span></div>
      </div>
        <div class="p-2 fs-normal">
            <div  v-for="(item,index) in data.chat" :key="index">
                <div class="d-flex flex-row-reverse align-items-end  pt-2" v-if="item.user_id===userinfo.id">
                    <div class="bd-highlight">
                        <com-avatar :i="userinfo.avatar" width="40" height="40" class="rounded-pill border border-2 border-white"/>
                    </div>
                    <div class="bd-highlight">
                        <Viewmy :show="item" />
                    </div>
                    <div class="bd-highlight">
                        <span v-if="item.state==='2'">未读</span>
                        <span v-if="item.state==='1'">已读</span>
                        <span v-if="item.state==='0'">发送失败</span>
                    </div>
                </div>
                <div class="d-flex flex-row align-items-end pt-2" v-else-if="item.user_id===data.user.user_id">
                    <div class="bd-highlight">
                        <com-avatar :i="data.user.avatar" width="40" height="40" class="rounded-pill border border-2 border-white"/>
                    </div>
                    <div class="bd-highlight">
                        <Viewta :show="item" />
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div ref="foot" class="bg-white p-3">
        <div class="row g-0">
            <div class="col-auto">
                <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-dark me-3 bi bi-mic-mute-fill" style="width:25px; height: 25px;"></span>
            </div>
            <div class="col">
                <div class="d-grid">
                    <input ref="input"  class="form-control form-control-sm bg-light border-0" v-model="text" @keyup="onvalue" placeholder="请输入内容" @click="onisinfo(false)"/>
                </div>
            </div>
            <div class="col-auto">
                <div v-if="text" class="px-2">
                    <button class=" btn-sm btn-success" @click="onsubmit({text:text})" :disabled="loading">发送{{loading?'..':''}}</button>
                </div>
                <div v-else>
                    <span v-if="chat_data.length" class="d-inline-block text-center border rounded-pill bg-white fs-6 text-dark ms-3 bi" :class="isInfo===true?'bi-grid-3x2-gap':'bi-chat-dots'" @click="onisinfo()" style="width: 25px; height: 25px;"></span>
                    <com-onselect :type="'amap'" :data="{url:'amap',type:'PositionPicker',city:'湛江',keyword:'',placeholder:'dsa',isshow:0}" class="d-inline-block text-center border rounded-pill bg-white fs-6 text-dark ms-3 bi bi-geo-alt" style="width: 25px; height: 25px;" @onsubmit="onsubmit"></com-onselect >
                </div>
            </div>
        </div>
    </div>
    <div v-if="isInfo" class="overflow-auto" :style="{height:submitheight+'px'}">
        <div  v-for="(item2,index2) in chat_data" :key="index2" class="bg-white border-bottom border-light p-2" @click="onsubmit({text:item2})">{{item2}}</div>
    </div>
</div>
</template>


<script>
import ViewGroup from '../group/start'
import Viewmy from './chat/my';
import Viewta from './chat/ta';
import axios from 'axios'
export default {
  components: {
    ViewGroup,
      Viewmy,
      Viewta,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
      return {
        isInfo:false,
        loading:false,
        submit_int:0,
        text:"",
        infolist:[],
        bodyheight:0,
        headheight:0,
        footheight:0,
        submitheight:41*5,
        chatparams:{},
        winnerHeight:0,
        "chat_data":[],
        userinfo:this.$store.state.userinfo,
      }
  },
  created(){
    if(this.$store.state.status==='error'){
        this.$tourl('login');
    }else if(this.$store.state.status==='success'){
        this.userinfo=this.$store.state.userinfo;
    }else{
        this.$root.$on('setuserstate',(msg)=>{ 
          if(msg==='ok'){
            if(this.$store.state.status==='success'){
                this.userinfo=this.$store.state.userinfo;
            }else{
                this.$tourl('login');  
            }
          }else{
            this.$tourl('/');  
          }
        });
    }
  },
   mounted(){
    this.headheight=this.$refs.head.offsetHeight;
    this.footheight=this.$refs.foot.offsetHeight;
    this.$refs.main.scrollTop=this.$refs.main.scrollHeight;
    this.winnerHeight=window.innerHeight;
    this.getisinfo();
    const _this = this;
    // 监听浏览器窗口变化
    window.onresize = function() {
      return (() => {
        _this.winnerHeight=window.innerHeight;
       _this.getisinfo();
      })();
    };
    if(this.data.push[3]==="d"){
        this.chat_data=["请问是2个人出行吗？","请问有携带宠物或者大件行李吗？","车上有亲友，你是否愿意同行？"];
        this.onisinfo(true);
        this.chatparams={token:this.$store.state.token,type:this.set.chat_type,tid:this.data.trip.id,nid:this.data.user.user_id};
    }else if(this.data.push[3]==="p"){
        this.chat_data=["有大件行李","帮我送个货，人不上车","有孕妇/老人，请多关照","我会佩戴口罩，请放心","有宠物，我会照看好"];
        this.onisinfo(true);
        this.chatparams={token:this.$store.state.token,type:this.set.chat_type,tid:this.data.trip.id,nid:this.data.user.user_id};
    }else{
        this.chatparams={token:this.$store.state.token,nid:this.data.user.user_id};
    }
  },
  updated(){
     this.getisinfo();
  },
  methods:{
    onvalue(){
    },
    onisinfo(key=''){
        if(key===''){
            this.isInfo=this.isInfo?false:true;
            if(this.isInfo===false && key===""){
            this.$refs.input.focus();
            }
        }else{
            this.isInfo=key;
        }
        this.getisinfo();
    }, 
    getisinfo(){
        this.$nextTick(() => {
            if(this.isInfo){
                this.bodyheight=this.winnerHeight-this.headheight-this.footheight-this.submitheight;
            }else{
                this.bodyheight=this.winnerHeight-this.headheight-this.footheight;
            }
            this.setck();
        })

    },
    setck(){
        this.$nextTick(() => {
        this.$refs.main.scrollTop=this.$refs.main.scrollHeight;
        })
    },
    onsubmit(data){
        if(data.data){
            if(data.url==='amap'){
            data={data:{type:data.url,name:data.data.name,lng:data.data.lng,lat:data.data.lat}}
            }
        }
        if(this.loading===false){
            this.loading=true;
            this.submit_int++;
            axios({method: 'post',url: this.$store.state.apiset.url+'/chat/sendout',params:this.chatparams,data:data}).then(response => {
                this.loading=false;
                let res = response.data;
                if(this.data.chat.length!==res.data.length){
                    this.data.chat = res.data;
                    this.getisinfo();
                }
            }).catch(function (error) {this.loading=false;console.log(error)});
            this.text='';
        }
    },
  }
}
</script>

<style>

</style>


