import Vue from 'vue'
import Router from 'vue-router'
import Common from './common'

Vue.use(Router)
export default new Router({
    routes: [{
        path: '/',
        redirect: '/passenger'
    }, {
        path: '/passenger',
        name: 'passenger',
        component: Common,
        children: [{
            path: '*',
        }]
    }, {
        path: '/driver',
        name: 'driver',
        component: Common,
        children: [{
            path: '*',
        }]
    }]
})


