<template>
  <div>
    <div v-if="data.style===1" class="rounded p-2 d-flex align-items-center" @click="data.isActive=true">
        <div class="d-inline bg-primary rounded-circle p-1 ms-2"><div class="d-block bg-white rounded-circle" style="width: 3px; height: 3px;"></div></div>
        <div class="ps-2" v-if="form[name]">你将从 <span class="text-primary fs-normal fw-lighter">{{form[name]}}</span> 上车</div>
        <div class="ps-2" v-else>{{data.placeholder}}</div>
    </div> 
    <div v-if="data.style===2" class="bg-info rounded p-2 d-flex align-items-center bg-gradient" @click="data.isActive=true">
        <div class="position-relative d-inline">
            <div class="spinner-grow text-success" ></div>
            <div class="position-absolute  bg-success rounded-circle p-1" style="top:11px;left:11px;">
                <div class="d-block bg-primary bg-white rounded-circle" style="width: 3px; height: 3px;"></div>
            </div>
        </div>
        <div class="ps-2 fw-bold">{{form[name]?form[name]:data.placeholder}}</div>
    </div> 
    <div v-if="data.style===3" class="rounded p-2 d-flex align-items-center" @click="data.isActive=true">
        <div class="d-inline bg-primary rounded-circle p-1 ms-2"></div>
        <div class="ps-2" v-if="form[name]">{{form[name]}}</div>
        <div class="ps-2" v-else>{{data.placeholder}}</div>
    </div> 
    <div v-if="data.style===4" class="rounded p-2 d-flex align-items-center" @click="data.isActive=true">
        <div class="d-inline bg-success rounded-circle p-1 ms-2"></div>
        <div class="ps-2" v-if="form[name]">{{form[name]}}</div>
        <div class="ps-2" v-else>{{data.placeholder}}</div>
    </div> 
    <LayoutFrom :data="data" :isActive="data.isActive" @onsubmit="onsubmit" @onsearch="onsearch" @isActive="data.isActive=false"/>
   </div>  
</template>

<script>
import LayoutFrom from './layout-search';
import axios from 'axios'
export default {
  components: { 
    LayoutFrom,
  },
  props: {
    name:"",
    data:{},
    form:{},
  },
  mounted(){
    window.ysdt._this=this;
    if(this.data.geolocation===true){
      window.ysdt.geolocation(this);
    }
    if(this.data.PositionPicker===true){
      window.ysdt.PositionPicker(this);
    }
  },
  methods:{
      onsubmit(key){
        this.form[this.name]=key.name;
        this.form[this.name+'_lat']=key.lat;
        this.form[this.name+'_lng']=key.lng;
        this.data.isActive=false;
        if(this.data.PositionPicker===true){
          window.ysdt.setcenter([key.lng,key.lat]);
        }
        
        this.$emit('getform');
      },
      onsearch(city, keyword){
         window.ysdt.getsearch(city,keyword).then((e) => {
            this.data.search=e;
         });
      },
      getnamedata(key){
        this.form[this.name]=key.name;
        this.form[this.name+'_lat']=key.lat;
        this.form[this.name+'_lng']=key.lng;
        this.data.selected=key;
      },
      nogeolocation(){
        if(!this.$store.state.origin){
          return ""
        }
        this.getnamedata({'name':this.$store.state.origin,'lat':this.$store.state.origin_lat,'lng':this.$store.state.origin_lng});
      },
      getgeolocation(key){
        this.form[this.name]=key.name;
        this.form[this.name+'_lat']=key.lat;
        this.form[this.name+'_lng']=key.lng;
        this.data.selected=key;
        localStorage.setItem('origin', key.name);
        localStorage.setItem('origin_lng', key.lng);
        localStorage.setItem('origin_lat', key.lat);
        // 更新token
        this.$store.commit('set_origin', key);
        axios({method: 'post',url: this.$store.state.apiset.url+'/edituser/geolocation',params:{token:this.$store.state.token},data:{origin:key.name,origin_lng:key.lng,origin_lat:key.lat}}).then(response => {
        }).catch(function (error) {this.loading=false;console.log(error)});
      }
  }
}
</script>

<style>

</style>


