<template>
   <div v-if="data.type">
      <div class="row">
        <div class="col">
          <div class="fs-5 fw-bold" v-html="data.title"></div>
          <div class="text-black-50 py-1" v-html="data.text"></div>
        </div>
        <div class="col-auto">
          <slot></slot>
        </div>
      </div>
   </div>
   <div v-else>
      <div class="fs-5 fw-bold" v-html="data.title"></div>
      <div class="text-black-50 py-1" v-html="data.text"></div>
   </div>
</template>
<script>
export default {
  props:{
    data:{},
  },
}
</script>


