<template>
    <div @click="onselect"><slot></slot></div>
</template>

<script>
export default {
  props:{
    type:'',
    name:'',
    data:'',
  },
  methods:{
    onselect(){
      this.type=this.type?this.type:'select';
      this.$toast({type:this.type,data:this.data}).then((e) => {
         this.$emit('onsubmit',{method: 'post',url:e.url,data:e.data});
      });
    }
  }
}
</script>


