<template>
      <span class="d-inline-block text-center border rounded-pill bg-white bi fs-6 text-dark" :class="'bi-'+icons+''" style="width:30px; height:30px;" @click="url?$tourl(url):''"></span>
</template>

<script>
export default {
  props:{
    url:'',
    icons:'',
  },
}
</script>


