import normal from "./normal";
import cityinner from "./cityinner";
import searchAmap from "./search-amap";
import pList from "./p-list";
import pSearch from "./p-search";
import dSearch from "./d-search";
import dTrip from "./d-trip";
import dPending from "./d-pending";
import evaluate from "./evaluate";
export default {
    template: `<component :is="type" :set="set" :data="data" @click="$emit('click')" @geturl="onurl"  @getinfo="getinfo"></component>`,
    components: {
        normal,
        cityinner,
        searchAmap,
        pList,
        pSearch,
        dTrip,
        dSearch,
        dPending,
        evaluate,
    },
    props: {
        type: {},
        set: {},
        data: '',
    },
    methods: {
        getinfo(key) {
            this.$emit('getinfo', key);
        },
        onurl(name, id, query = "") {
            if (this.set.url[name]) {
                this.$tourl(this.set.url[name] + id, query)
            }
        },
    }
}


