<template>
     <input type="text" class="form-control" :class="{'border-danger':data.validate}" :placeholder="data.placeholder" v-model="form[name]" @keyup="onvalue"/>
</template>

<script>
export default {
  props: {
    name:"",
    data:{},
    form:{},
  },
  methods:{
    onvalue(){
      this.$emit('getform');
      if(typeof this.data.validate!=='undefined'){
          this.data.validate=false
      }
    }
  }
}
</script>

<style>

</style>


