<template>
  <div class="app-toast">
    <div v-if="data.type==='dark_center'" class="position-fixed top-50 start-50  translate-middle rounded-pill text-white py-1 px-3 d-filter shadow-sm" :class="'bg-'+bg">
      <div>{{data.msg}}</div>
    </div>
    <div v-else-if="data.type==='dark_bottom'" class="position-fixed start-50 translate-middle rounded-pill text-white py-1 px-3 d-filter shadow-sm" style=" bottom: 10%!important" :class="'bg-'+bg">
      <div>{{data.msg}}</div>
    </div>
    <div v-else class="bg-white shadow-sm rounded-2 p-2 text-dark" style="transform: translate(-50%, -50%);">
      <div class="text-center fw-bold">温馨提示</div>
      <div class="p-2 text-center">{{data.msg}}</div>
      <div class="d-grid"><button class=" btn btn-primary" @click="$emit('getinfo',{type:0})">关闭</button></div>
    </div>
  </div>
</template>
<script>
export default {
  props:{
    data:{}
  },
  data(){
    return {
      bg:'dark',
    }
  },
  created(){
    if(this.data.code===301){
      this.bg='danger';
      this.data.type='dark_center';
    }else if(this.data.code===201){
      this.bg='success';
      this.data.type='dark_center';
    }else if(this.data.code===62){
      this.bg='success';
      this.data.type='dark_center';
    }else{
      this.data.type='dark_center';
    }
    if(['dark_center','dark_bottom'].includes(this.data.type)){
     setTimeout(() => {
         this.$emit('getinfo',{type:0});
     }, 3000);
     }
  }
}
</script>
<style>

</style>


