<template>
    <div class="bg-light">
        <div class="position-fixed top-0 start-0 end-0 bottom-0  bg-light">
            <div id="container" class="h-100"></div>
        </div>
        <div class="position-fixed top-0 start-0">
        <span @click="$tourl('../')"  class="fs-4 bi bi-chevron-left m-2"></span>
        </div>
        <div class="bg-white position-fixed start-0 end-0 bottom-0 rounded-top-lg px-3 pb-1">
            <div class="text-center" @click="isshow=isshow==='data'?'':'data'"><span class="bi fs-2 text-black-50" :class="isshow==='data'?'bi-chevron-compact-up':'bi-chevron-compact-down'"></span></div>
            <div v-for="(item,key) in info[binding][data.show.state]" :key="key" class="pb-1 fs-normal">
                <component v-if="isshow===item?false:true" :is="'trip-'+item" :set="set" :binding="binding" :data="data" :show="show" @onsubmit="onsubmit" @ontoast="ontoast" @onsubid="onsubid" ></component>
            </div>
        </div>
    </div>
</template>

<script>
import tripInfo from './trip-info'
import tripInfoDate from './trip-info-date'
import tripInfoPay from './trip-info-pay'
import tripInfoCancel from './trip-info-cancel'
import tripCarpooling from './trip-carpooling'

import tripPay from './trip-pay'
import tripPayok from './trip-payok'
import tripUser from './trip-user'
import tripData from './trip-data'
import tripDataDriver from './trip-data-driver'
import tripNav from './trip-nav'
import tripSubmit from './trip-submit'
import tripSubmitNo from './trip-submit-no'
import tripSubmitAgain from './trip-submit-again'
import tripEvaluate from './trip-evaluate'
export default {
  components: {
    tripInfo,
    tripInfoDate,
    tripInfoPay,
    tripInfoCancel,
    tripCarpooling,
    tripPay,
    tripPayok,
    tripUser,
    tripData,
    tripDataDriver,
    tripNav,
    tripSubmit,
    tripSubmitNo,
    tripSubmitAgain,
    tripEvaluate,
  },
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  },
  data(){
    return {
      isshow:'',
      info:{
        "p": {
            "0": ["info"],
            "1": ["user",  "data-driver", "submit"],
            "2": ["info", "user", "data", "nav", "submit"],
            "22": ["info", "user", "data", "nav", "submit"],
            "31": ["info", "user", "pay", "data", "nav", "submit"],
            "32": ["info", "user", "pay", "data", "nav", "submit"],
            "33": ["info", "user", "pay", "data", "nav", "submit"],
            "41": ["info", "user", "pay", "data", "nav", "submit"],
            "42": ["info", "user", "pay", "data", "nav", "submit"],
            "43": ["info", "user", "pay", "data", "nav", "submit"],
            "51": ["info", "user", "pay", "data", "nav", "submit"],
            "52": ["info", "user", "pay", "data", "nav", "evaluate"],
            "53": ["info", "user", "pay", "data", "nav", "evaluate"],
            "71": ["info-cancel", "data", "nav", "submit"],
            "72": ["info-cancel", "data", "nav", "submit"],
            "81": ["info-cancel", "data", "nav", "submit"],
            "82": ["info-cancel", "data", "nav", "submit"],
            "83": ["info-cancel", "data", "nav", "submit"],
            "91": ["info-cancel", "data", "nav", "submit"]
        },
        "d": {
            "0": ["info"],
            "1": ["user", "data-driver", "submit"],
            "2": ["info-pay", "user", "data", "nav", "submit"],
            "22": ["info-pay", "user", "data", "nav", "submit"],
            "31": ["info-pay", "user", "payok", "data", "nav", "submit"],
            "32": ["info-pay", "user", "payok", "data", "nav", "submit"],
            "33": ["info-pay", "user", "payok", "data", "nav", "submit"],
            "41": ["info-pay", "user", "payok", "data", "nav", "submit"],
            "42": ["info-pay", "user", "payokpay", "data", "nav", "submit"],
            "43": ["info-pay", "user", "payok", "data", "nav", "submit"],
            "51": ["info-pay", "user", "payok", "data", "nav", "evaluate"],
            "52": ["info-pay", "user", "payok", "data", "nav", "submit"],
            "53": ["info-pay", "user", "payok", "data", "nav", "evaluate"],
            "71": ["info-cancel", "data","nav", "submit-again"],
            "72": ["info-cancel", "data", "nav","submit-again"],
            "81": ["info-cancel", "data","nav", "submit-again"],
            "82": ["info-cancel", "data", "nav","submit-again"],
            "91": ["info-cancel", "data","nav", "submit-again"]
        }
      }
    }
  },
  mounted(){
    window.ysdt._this=this;
    if(this.show.origin && this.show.destination){
      window.ysdt.showdriving([this.show.origin_lng,this.show.origin_lat],[this.show.destination_lng,this.show.destination_lat]);
    }
  },
  methods:{
    onsubmit(key){
        this.$emit('onsubmit',key);
    },
    ontoast(state,key){
      if(state){
        if(key.url){
          if(key.url[1]){
          this.$tourl(key.url[0]+this.show[key.url[1]]);
          }else{
          this.$tourl(key.url[0]); 
          }
        }
        if(key.href){
          window.location.href=key.href[0]+this.show[key.href[1]];
        }
      }else{
        this.$toast({type:'tips',data:key.toast});
      }
    },
    onsubid(key){
      key['data'].id=this.show.id;
      if(this.data.push[2]){
      key['data'].trip_token=this.data.push[2];
      }
      this.$emit('onsubmit',{method: 'post',url:key.url,params:{token:this.$store.state.token},data:key.data});
    },
  }
}
</script>


