<template>
   <div class="row">
      <Toast-button :data="item"  v-for="(item,key) in data" :key="key" class="col" @run_toast="run_toast"/>
   </div>
</template>
<script>
import ToastButton from './toast-button'
export default {
  components:{
    ToastButton,
  },
  props:{
    data:{},
  },
  methods:{
    run_toast(key){
        this.$emit('run_toast',key);
    }
  }
}
</script>


