<template>
  <div>
      <div @click="data.isActive=true">{{form[name]|setdate('',data.placeholder)}}</div>
      <LayoutFrom :isActive="data.isActive" @onsubmit="onsubmit" @isActive="onActive">
          <Inner-pickers-date :form="fromdate"  @onsubmit="onsubmit"/>
      </LayoutFrom>
   </div>  
</template>

<script>
import InnerPickersDate from './inner-pickers-setdate'
import LayoutFrom from './layout-bottom';
export default {
  components: { 
    LayoutFrom,
    InnerPickersDate,
  },
  props: {
    name:"",
    data:{},
    form:{},
  },
  data(){
      return {
          selected:'',
          picktype:'startdate',
          fromdate:'',
      }
  }
  ,methods:{
      onActive(){
        this.data.isActive=false;
      },
      onsubmit(key){
        this.form[this.name]=key;
        this.form[this.name+'_end']=new Date(new Date(key).getTime() + 30 * 60 * 1000).format("yyyy-MM-dd h:mm");
        this.$emit('getform',this.name,this.form[this.name]);
        this.onActive();
      }
    }
}
</script>



