<template>
<div>
   <div v-if="auth_loading">
      <Layout v-if="data.set" :type="data.type" :set="data.set" :data="data.data" :group="data.group" @refresh="ispathaxios" @onsubmit="onsubmit"/>
      <Prompt v-if="prompt" :data="prompt" />
      <div v-if="loading" class="position-fixed top-0 start-0 end-0 bottom-0 ">
        <div class="position-absolute top-50 start-50 translate-middle"><span class="spinner-border"></span></div>
      </div>
   </div>
   <div v-else>
     <div class="position-fixed top-0 start-0 end-0 bottom-0 ">
        <div class="position-absolute top-50 start-50 translate-middle">
          <div class="text-center">
            <div><span class="spinner-border"></span></div>
            <div class="text-black-50 py-2">正在加载中..</div>
            <div class="fs-3 text-primary">欢迎使用同泰出行</div>
          </div>
        </div>
      </div>
   </div>
</div>
</template>

<script>
import pathmethods from '@/router/axios-ajax'
import Layout from '../components/layout/start'
import Prompt from '../components/prompt/start'
export default {
  components: {
    Layout,
    Prompt,
  },
  data() {
    return {
      data:{},
      viewkey:'index',
      tips:[],
      auth_loading:false,
      loading:false,
      datacache:this.$store.state.datacache,
      prompt:'',
    };
  },
  created () {
    this.auth_user();
  },
  watch:{
     $route(){
        this.auth_user();
     }
  },
  methods:pathmethods
};
</script>


