<template>
    <Layout :head_class="'bg-light'" :main_class="'bg-light'" :foot_class="'bg-white'" >
        <template slot="main">
            <div class="position-relative">
                <div class="position-absolute start-0 end-0 bottom-0">
                    <div class="rounded bg-white p-2">
                        <ViewForm :data="set.form" :form="form" @getinfo="getinfo"/>
                        
                        <div class="d-grid"><button class=" btn btn-primary" @click="onsubmit">预约顺路车</button></div>
                    </div>
                </div>
                <div class="position-absolute start-0 end-0 top-0">
                      <View-group v-for="(item,key) in group" :key="key" :set="item.set" :type="item.type" :data="data" />
                </div>
            </div>
        </template>
    </Layout>
</template>

<script>
import Layout from './public-amap'
import ViewGroup from '../group/start'
import Iconspill from "../block/icons/pill-icons";
import ViewForm from "../form/p-edit-start";
import infoprice from '@/json/setprice.json';
export default {
  components: {
    Layout,
    ViewGroup,
    ViewForm,
    Iconspill,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
        return {
            infoprice:infoprice,
            form:{},
        }
  },
  created(){
    this.form=this.set.forminfo;
  },
  mounted(){
    ysdt._this=this;
    ysdt.getdriving([this.$route.query.origin_lng,this.$route.query.origin_lat],[this.$route.query.destination_lng,this.$route.query.destination_lat])
  },
  methods:{
      onvalidate(){
          for(let x in this.form){
            if(this.form[x]==="" || this.form[x].length===0){
              if(this.set.form[x]){
                if(this.set.form[x].validate){
                this.set.form[x].isActive=true
                return 0;
                }
              }
            }
          }
          return 1;
      },
      onsubmit(){
        if(this.onvalidate()===1){
         this.$emit('onsubmit',{method: 'post',url: '/passenger/riding',params:{token:this.$store.state.token},data:this.form,});
        }
      },
      onprice(people,distance,thankfee=0,tolls=0){   
          people=people<1?1:people;  
          distance=(distance * 0.001).toFixed(0);
          let dtype="3";
          if(distance<3){
              dtype="3";
          }else if(distance>=3 && distance<10){   
              dtype="3-10";
          }else if(distance>=10 && distance<30){   
              dtype="10-30";
          }else if(distance>=30 && distance<50){   
              dtype="30-50";
          }else if(distance>=50 && distance<100){   
              dtype="50-100";
          }else{
              dtype="100+";
          }
          let p_price=Number(this.infoprice[people][dtype][0]*Number(distance)+Number(thankfee)+Number(tolls));
          let n_price=Number(this.infoprice[people][dtype][1]*Number(distance)+Number(thankfee)+Number(tolls));
          let price=Number(this.infoprice[people][dtype][2]*Number(distance)+Number(thankfee)+Number(tolls));
          if(price<20){
          p_price=p_price+(20-price);
          n_price=n_price+(20-price);
          price=price+(20-price);
          }
          this.$set(this.form,'p_price',p_price.toFixed(1));  
          this.$set(this.form,'n_price',n_price.toFixed(1));  
          this.$set(this.form,'price',price.toFixed(1)); 
      },
      getinfo(key){
        this.onprice(this.form.people,this.form.distance,this.form.thankfee,this.form.tolls);
        this.onvalidate();
      },
      getprice(key){
      this.form['tolls']=key.tolls;
      this.form['distance']=key.distance;
      this.form['aboutminutes']=key.aboutminutes;
      this.form['origin']=this.$route.query.origin;
      this.form['origin_lat']=key.origin_lat;
      this.form['origin_lng']=key.origin_lng;
      this.form['destination']=this.$route.query.destination;
      this.form['destination_lat']=key.destination_lat;
      this.form['destination_lng']=key.destination_lng;
      this.getinfo(this.form);
      }
  }
}
</script>



