<template>
  <component :is="type?type:'login'" :set="set" :group="group" :data="data" @getinfo="getinfo" @onsubmit="onsubmit"/>
</template>

<script>
import login from "./login";
import show from "./show";
import chatarr from "./chatarr";
import chat from "./chat";
import myuser from "./myuser";


import user from "./user";
import showScroll from "./show-scroll";
import amapShow from "./amap-show";
import driver from "./driver";
import driverLogin from "./driver-login";
import passengerIndex from "./passenger-index";
import passengerEdit from "./passenger-edit";
import edit from "./edit";
export default {
  components: { 
      login,
      show,
      chatarr,
      chat,
      myuser,
      user,
      edit,
      amapShow,
      driver,
      driverLogin,
      passengerIndex,
      passengerEdit,
      showScroll,
  },
  props: {
    type:'',
    set:{},
    group:'',
    data:{},
  },
  methods:{
    getinfo(key){
        this.$emit('getinfo',key);
    },
    onsubmit(key){
      this.$emit('onsubmit',key);
    },
  }
}
</script>


