<template>
    <div class="row align-items-center py-2">
        <div class="col d-flex flex-row bd-highlight" v-if="binding==='d'">
            <div class="bd-highlight">
                <com-avatar :i="show.avatar" width="50" height="50"/>
            </div>
            <div class="bd-highlight ms-2">
                <div class="fw-bold fs-6">{{show.nickname}}</div>
                <div>{{show.pevaluate}} 分 - 出行<span class="text-primary">{{show.pint}}</span>次</div>
            </div>
        </div>
        <div class="col" v-else>
          <div class="d-flex flex-row align-items-center">
            <div class="bd-highlight position-relative stretched-link" @click="$tourl('user-'+show.duser_id)"><img :src="show.imgurl" width="50" height="50" class="rounded-pill"></div>
            <div class="bd-highlight ms-1">
              <div class="fw-bold fs-6">{{show.brand}}</div>
              <div><span>{{show.typeinfo}} {{show.colour}}</span><span class="ms-1">{{show.devaluate.toFixed(1)}}分</span><span class="ms-1">接单{{show.dint}}次</span></div>
            </div>
          </div>
        </div>
        <div class="col-auto">
            <toast-nav :data="setdata(show.state)" @run_toast="run_toast"/>
        </div>
    </div>
</template>

<script>
import Iconspill from "../../block/icons/pill-icons";
export default {
  components: {
    Iconspill,
  },
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  },
  data(){
    return {
      toastnav:[
        {type:'tab-dark',state:"0",bi:'telephone-fill',stateinfo:{"0":{toast:{type:'tips',data:{text:'请接单后,才可电话联系'}}},"1":{tel:this.show.mobile}}},
        {type:'chat',state:"0",bi:'chat-dots-fill',int:'',stateinfo:{"0":{toast:{type:'tips',data:{text:'请接单后,才可以发消息'}}},"1":{tourl:'chat-'+this.getid()+'-'+(this.binding==='p' && this.show.state==='1'?this.show.id+'-'+'a':this.show.id)+'-'+this.binding}}},
      ],
      mobile:{
        p:['2','22','31','32','33','41','42','43','51','52'],
        d:['2','22','31','32','33','41','42','43','51','52']
      },
      chat:{
        p:['1','2','22','31','32','33','41','42','43','51','52','53'],
        d:['1','2','22','31','32','33','41','42','43','51','52','53']
      }
    }
  },
  methods:{
    chatname(){
      let sid,tid
       sid=this.$store.state.userinfo.id;
       tid=this.getid();
      return sid>tid?tid+','+sid:sid+','+tid;
    },
    getid(){
      if(this.binding==='p'){
        if(this.show.state==='1'){
          return this.show.user_id;
        }else{
          return this.show.duser_id;
        }
      }else if(this.binding==='d'){
       return this.show.user_id;
      }
    },
    run_toast(key){
        this.$emit('onsubmit',{method:'post',url:key.url,data:key.data});
    },
    setdata(state){
      let data=this.toastnav;
      data[0].state=this.mobile[this.binding].includes(state)?1:0;
      data[1].state=this.chat[this.binding].includes(state)?1:0;
      data[1].int=this.$store.state.chatarr[this.chatname()] && this.$store.state.chatarr[this.chatname()].int>0?this.$store.state.chatarr[this.chatname()].int:'';
      return data;
    }
  }
}
</script>



