<template>
  <div class="d-inline-bloc">
      <div class="d-inline-block p-1 px-2 border rounded-pill text-center"  @click="data.isActive=true">{{form[name]|setdate(form[name+'_end'],data.placeholder)}}</div>
      <LayoutFrom :isActive="data.isActive" @onsubmit="onsubmit" @isActive="onActive">
          <Inner-pickers-date :form="fromdate"  @onsubmit="onsubmit"/>
      </LayoutFrom>
   </div>  
</template>

<script>
import InnerPickersDate from './inner-pickers-date'
import LayoutFrom from './layout-bottom';
export default {
  components: { 
    LayoutFrom,
    InnerPickersDate,
  },
  props: {
    name:"",
    data:{},
    form:{},
  },
  data(){
      return {
          selected:'',
          picktype:'startdate',
          fromdate:{startdate:'',enddate:''},
      }
  }
  ,methods:{
      onActive(){
        this.data.isActive=false;
      },
      onsubmit(key){
        this.form[this.name]=key.startdate;
        this.form[this.name+'_end']=key.enddate;
        this.$emit('getform',this.name,this.form[this.name]);
        this.onActive();
      }
    }
}
</script>



