<template>
    <div>
        <div class="bg-dark position-fixed top-0 start-0 end-0 bottom-0"  style=" opacity: .5;"  @click="$emit('isActive')"></div>
        <div class="bg-white position-fixed start-0 end-0 bottom-0 rounded-top-lg ysanim ysbtp text-dark">
            <div class="position-absolute top-0 end-0">
                <div class="m-2"><div class="bi bi-x fs-2"  @click="$emit('isActive')"></div></div>
            </div>
            <div class="p-3">
                <div class="fs-6 fw-bold p-2 text-center">已评价{{binding==='d'?'乘客':'司机'}}</div>
                <div class="fs-1 text-center d-flex justify-content-around text-black-50">
                    <div  v-for="n in 5" :key="n"  class="bd-highlight">
                        <span class="bi bi-star-fill" :class="{'text-warning':n<=star}"></span>
                    </div>
                </div>
                    <div class="text-center text-secondary p-3">{{starname[star]}}</div>
                <div class="row row-cols-2 g-2 fs-normal text-secondary">
                    <div v-for="(item,index) in selected" :key="index"  class="col">
                        <div  class="border rounded p-1 text-center border-info text-info">{{starlabel[0][item]?starlabel[0][item]:starlabel[1][item]}}</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
binding:'',
    star:0,
    selected:{},
    starname:{},
    starlabel:{},
  }
}
</script>



