import vue from 'vue'
import toastComponent from './toast.vue'

// 组件构造器，构造出一个 vue组件实例
const ToastConstructor = vue.extend(toastComponent)
import toastSet from "./toast-set.vue";
import toastTips from "./toast-tips.vue";
import toastResult from "./toast-result.vue";
import toastSelect from "./toast-select.vue";
import toastPay from "./toast-pay.vue";
import toastBlock from "./toast-block.vue";
import toastAmap from "./toast-amap.vue";
import toastCancelTrip from "./toast-cancel-trip.vue";
var showToast = function({ type, data, duration = 0 }) {
    return new Promise(function(resolve, reject) {
        const toastDom = new ToastConstructor({
                el: document.createElement('div'),
                template: `<component v-if="isShow" :is="'toast-'+type" :data="data" @getinfo="getinfo"></component>`,
                components: {
                    toastSet,
                    toastTips,
                    toastResult,
                    toastSelect,
                    toastPay,
                    toastBlock,
                    toastAmap,
                    toastCancelTrip,
                },
                data() {
                    return {
                        isShow: true, // 是否显示
                        data: data, // 文本内容
                        type: type // 类型
                    }
                },
                methods: {
                    getinfo(key) {
                        if (key.type === 0) {
                            toastDom.isShow = false
                        } else if (key.type === 1) {
                            resolve(key.data)
                            toastDom.isShow = false
                        } else {
                            resolve(key)
                        }
                    }
                }
            })
            // 添加节点
        document.body.appendChild(toastDom.$el)
            // 过渡时间
        if (duration > 0) {
            setTimeout(() => {
                toastDom.isShow = false
            }, (duration * 1000))
        }
        return
    });
}
showToast.set = function(data) {
    return showToast({ data: data, type: 'set' });
}
showToast.tips = function(text) {
    return showToast({ data: { text: text }, type: 'tips' });
}

function showUrl(url, query = "") {
    if (this.$route.path.split('/').pop() === url) {
        return
    }
    if (url.substr(0, 1) === "/") {
        this.$router.push({ path: url, query: query });
    } else {
        let patharr = this.$route.path.split('/');
        for (let i = 0; i < patharr.length; i++) {
            if (patharr[i] == "" || typeof(patharr[i]) == "undefined") {
                patharr.splice(i, 1);
                i = i - 1;
            }
        }
        if (url.substr(0, 2) === "./") {
            patharr.pop();
            url = url.replace("./", "")
        } else if (url.substr(0, 6) === "../../") {
            patharr.pop();
            patharr.pop();
            url = url.replace("../../", "")
        } else if (url.substr(0, 3) === "../") {
            patharr.pop();
            url = url.replace("../", "")
        }
        let pathurl = patharr.join('/');
        pathurl = url ? pathurl + '/' + url : pathurl;
        this.$router.push({ path: '/' + pathurl, query: query });
    }
}

function showdd(data) {
    return new Promise(function(resolve, reject) {
        resolve(data)
            //resolve(Object.assign({}, data))
    });
}

// 全局注册
function registryToast() {
    vue.prototype.$tourl = showUrl
    vue.prototype.$toast = showToast
    vue.prototype.$todd = showdd
}

export default registryToast


