<template>
  <div>
    <div class="position-fixed top-0 start-0 end-0 bottom-0 bg-dark" style="opacity: .5;" @click="$emit('getinfo',{type:0})"></div>
    <div class="bg-light position-fixed start-0 end-0 top-50 bottom-0  rounded-top-lg" >
        <div class="position-absolute top-0 start-0 end-0 p-2 px-3 bg-light rounded-top-lg" style="z-index:3">
          <div class="d-flex justify-content-between align-items-center">
              <div class="bd-highlight  fs-6 fw-bold" v-if="data.data">你有 <span class="text-primary">{{data.data.length}}</span> 个进行的行程</div>
              <div class="bd-highlight"  @click="$emit('getinfo',{type:0})"><span class="bi bi-x fs-2"></span></div>
          </div>
        </div>
        <div class="h-100 overflow-auto p-2">
            <div class="p-4"></div>
            <ViewList :set="data.set" :data="data.data" @getinfo="getinfo"/>
        </div>
    </div>
  </div>
</template>
<script>
import ViewList from '../components/block/start';
export default {
  components: { 
    ViewList,
  },
  props:{
    data:{}
  },
    methods: {
        getinfo(key) {
            this.$emit('getinfo',{type:1,data:key});
        },
    }
}
</script>

<style>

</style>


