<template>
 <com-layout :layout_class="set.layout_class" :head_class="set.head_class" :main_class="set.main_class" :foot_class="set.foot_class">
    <template slot="head">
      <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="item.data" />
    </template>
    <template slot="main">
      <div class="rounded bg-white  m-3  p-2">
        <div class="border-bottom border-light"><Viewinput :name="set.dwnames[0]" :data="set.form.data[set.dwnames[0]]"  :form="form" @getform="getinfo" /></div>
        <div class="border-bottom border-light"><Viewinput :name="set.dwnames[1]" :data="set.form.data[set.dwnames[1]]"  :form="form" @getform="getinfo" /></div>
        <div class="row text-black-50 p-2">
          <div class="col">
            <div class="d-flex flex-row align-items-center">
              <div class="bd-highlight">
                <span class="fs-5 bi bi-stopwatch"></span>
              </div>
              <div class="bd-highlight ms-2">
                <Viewinput :name="set.dwnames[2]" :data="set.form.data[set.dwnames[2]]"  :form="form" @getform="getinfo" />
              </div>
            </div>
          </div>
          <div class="col">
            <div class="d-flex flex-row align-items-center">
              <div class="bd-highlight">
                <span class="fs-5 bi bi-person"></span>
              </div>
              <div class="bd-highlight ms-2">
                <Viewinput :name="set.dwnames[3]" :data="set.form.data[set.dwnames[3]]"  :form="form" @getform="getinfo" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
    <template slot="foot">
      <div class="d-grid p-3 bg-white"><button class=" btn" :class="alidate?'btn-success':'btn-dark'" @click="onsubmit" :disabled="alidate?false:true">发布并搜索</button></div>
    </template>
  </com-layout>
</template>

<script>
import Layout from './public-amap'
import ViewGroup from '../group/start'
import Viewinput from "../form/input";
export default {
  components: {
    Layout,
    ViewGroup,
    Viewinput,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
        return {
            form:{},
            alidate:false,
        }
  },
  created(){
    this.form=this.set.form.setform;
    if(this.set.form.setform_route){
      for(let x in this.set.form.setform_route){
         if(this.$route.query[this.set.form.setform_route[x]]){
            this.$set(this.form,this.set.form.setform_route[x],this.$route.query[this.set.form.setform_route[x]]);
         }
      }
    }
  },
  mounted(){
    this.onvalidate();
  },
  methods:{
      onvalidate(){
          for(let x in this.form){
            if(this.form[x]==="" || this.form[x].length===0){
              if(this.set.form.data[x]){
                if(this.set.form.data[x].validate){
                this.set.form.data[x].isActive=true
                this.alidate=false;
                return 0;
                }
              }
            }
          }
          this.alidate=true;
          return 1;
      },
      onsubmit(){
        if(this.onvalidate()===1){
         this.$emit('onsubmit',{method: 'post',url: this.set.form.accept,params:{token:this.$store.state.token},data:this.form,});
        }
      },
      getinfo(key){
        this.onvalidate();
      },
  }
}
</script>



