<template>
    <div class="d-inline-block m-1 border rounded-pill p-1 px-2" @click="$emit('click')">
        {{set.names[data.name]}}<span v-if="data.number"><span class="ms-1" >{{data.number}}</span></span>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
  },
}
</script>



