<template>
  <div>
    <div class="position-fixed top-0 start-0 end-0 bottom-0 bg-dark" style="opacity: .5;" @click="$emit('getinfo',{type:0})"></div>
    <div class="text-center bg-light position-fixed start-0 end-0 bottom-0">
        <div class="bg-white p-2 border-bottom border-light" v-for="(item,index) in data" :key="index"  @click="$emit('getinfo',{type:1,data:item})">{{item.name}}</div>
        <div class="bg-white p-2 mt-2" @click="$emit('getinfo',{type:0})">不取消了</div>
    </div>
  </div>
</template>
<script>
export default {
  props:{
    data:{}
  },
}
</script>

<style>

</style>


