<template>
    <div class="alert alert-danger p-1" v-if="show.ispay==='0'">
        <div class="d-flex justify-content-between">
            <div class="bd-highlight">系统暂时不支付在线支付，<br/>行程费用请司机确认收取</div>
            <com-onselect class="bd-highlight" :type="'pay'" :data="setdata(show.id)" @onsubmit="onsubmit"><button class=" btn-sm btn-warning">确认收款</button></com-onselect>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  },
  methods:{
    onsubmit(key){
       this.$emit('onsubmit',key);
    },
    setdata(id){
      return {binding:this.binding,fee:this.show.fee,form:{url:'/driver/payok',data:{id:id}}}
    }
  }
}
</script>

<style>

</style>


