<template>
<div class="container-fluid">
    <div class="bg-info bg-gradient text-white row align-items-center p-2 rounded-bottom">
        <div class="col">
            <span class="fs-6 me-3 fw-bold border-bottom border-2"  v-for="(item,index) in list" :key="index" :class="ispath===item.url?'border-white':'border-info'" @click="ispath===item.url?'':$tourl('/'+item.url)">{{item.name}}</span> 
        </div>
        <div class="col-auto">
             <span class="position-relative fs-4 bi bi-chat-text-fill" @click="$tourl('chatarr')">
               <span class="position-absolute start-100 translate-middle badge rounded-pill bg-danger fs-normal" style="top:8px;" v-if="getchatint()>0">{{getchatint()}}</span>
             </span>
             <span class="ms-3 fs-4 bi bi-person-lines-fill" @click="$tourl('myuser')"></span>
        </div>
    </div>
</div>
</template>

<script>
export default {
  components: { 
  },
  props: {
    set:{},
    data:{},
  },
  data(){
    return {
      list:[{name:'乘客',url:'passenger'},{name:'司机',url:'driver'}],
      ispath:""
    }
  },
  created(){
     this.ispath=this.$route.path.split('/').pop();
  },
  methods:{
    getchatint(){
      let i=0;
      for(let x in this.$store.state.chatarr){
        i=i+this.$store.state.chatarr[x].int;
      }
      return i;

    }
  }
}
</script>

<style>

</style>


