<template>
  <component :is="data.type?data.type:'normal'" :data="data" @getinfo="getinfo"/>
</template>

<script>
import normal from "./normal";
import trip from "./trip";
export default {
  components: { 
      trip,
      normal,
  },
  props: {
    data:{},
  },
  methods:{
    getinfo(key){
        this.$emit('getinfo',key);
    },
  }
}
</script>


