<template>
    <div :class="layout_class">
        <div class="position-fixed top-0 start-0 end-0" :style="{height:bodyheight+'px'}"  :class="head_class">
            <div id="container" class="h-100"></div>
        </div>
        <div class="position-fixed top-0 start-0">
        <span @click="$tourl('../')"  class="fs-4 bi bi-chevron-left m-2"></span>
        </div>
        <div class="bg-white position-absolute start-0 end-0"
         :class="main_class"
         :style="'top:'+bodyheight+'px'">
            <slot name="main"></slot>
            <div :style="'height:'+footheight+'px'"></div>
        </div>
        <div ref="foot" class="position-fixed start-0 end-0 bottom-0" :class="foot_class">
            <slot name="foot"></slot>
        </div>
    </div>
</template>

<script>

export default {
  props: {
    layout_class:'',
    head_class:'',
    main_class:'',
    foot_class:'',
  },
  data(){
        return {
            bodyheight:0,
            footheight:0,
            index:0,
            myScroll:null,
        }
  },
  mounted(){
    this.footheight=this.$refs.foot.offsetHeight;
    this.bodyheight=window.innerHeight-this.footheight;
    const _this = this;
    // 监听浏览器窗口变化
    window.onresize = function() {
      return (() => {
        _this.bodyheight = window.innerHeight-_this.footheight;
      })();
    };
    window.addEventListener('scroll',this.handleScroll);
    this.goto(200);
  },
  methods:{
    handleScroll () {
      var scrollTop =  document.documentElement.scrollTop || document.body.scrollTop
    //  console.log(scrollTop)
    },
    goto(index) {
      setTimeout(function(){
      document.body.scrollTop = document.documentElement.scrollTop=0;
      },100);
    }
  }
}
</script>

<style>

</style>


