<template>
  <div class="col">
     <div class="border rounded p-1 text-center fs-normal" @click="$emit('geturl','id',data.id)">
    {{data.name}}<span v-if="data.number"><span class="text-primary" >{{data.number}}</span>{{data.unit}}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
  },
}
</script>



