<template>
  <div>
    <div class="d-flex justify-content-between align-items-center p-2 border border-bottom border-light"  @click="onsubmit">
        <div class="bd-highlight fs-6">
            {{form[name]}}
        </div>
        <div class="bd-highlight">
            {{data.name}}
            <span class="bi bi-chevron-right"></span>
        </div>
    </div>
   </div>  
</template>

<script>
export default {
  props: {
    name:"",
    data:{},
    form:{},
  },
  methods:{
      onsubmit(){
        if(this.data.accept){
          this.$emit('onsubmit',{method: 'post',url:this.data.accept,params:{token:this.$store.state.token},data:this.data.accept_data});
        }
      },
  }
}
</script>



