<template>
  <div class="position-fixed top-0 start-0 end-0 bottom-0 bg-light" style="z-index:999">
        <div class="container-fluid bg-white py-2 border-bottom">
            <div class="row align-items-center">
                <div class="col">
                    <div class="input-group input-group-sm">
                        <span class="input-group-text">{{data.city}}</span>
                        <input class="form-control" value="" v-model="data.keyword" ref="input" @keyup="onsearch" :placeholder="data.placeholder" />
                    </div>
                </div>
                <div class="col-auto text-center" @click="$emit('getinfo',{type:0})">取消</div>
            </div>
        </div>
        <div :style="{height:bodyheight+'px'}">
            <div v-show="data.isshow===1"  class="h-100">
                <div id="container" class="h-100"></div>
                <div class="position-fixed bottom-0 start-50 translate-middle bg-white rounded p-2 mb-5" style="width: 85%;">
                    <div class="row">
                        <div class="col">{{selected.name}}</div>
                        <div class="col-auto">
                            <button class="btn btn-primary" @click="onsubmit(selected)">确定</button>
                        </div>
                    </div>
                </div>
            </div>
            <div v-show="data.isshow===0" class="h-100 bg-light overflow-auto">
                <div class="container-fluid">
                    <div v-for="(item,index) in data.search" :key="index">
                        <div class="row align-items-center bg-white" @click="onsubmit(item)">
                            <div class="col-auto">
                                <div class="py-1">
                                    <span class="bi fs-5 text-black-50" :class="'bi-'+(item.icons?item.icons:'clock')"></span>   
                                </div>
                            </div>
                            <div class="col p-0 border-bottom ">
                                <div class="d-flex justify-content-between align-items-center">
                                    <div class="bd-highlight py-1">
                                        <div>{{item.name}}</div>
                                        <div v-if="item.address" class="text-black-50 fs-normal"><span v-if="item.adname">{{item.adname}}-</span>{{item.address}}</div>
                                    </div>
                                    <div class="bd-highlight">
                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>
<script>
export default {
  props:{
    data:{}
  },
  data(){
      return {
          bodyheight:0,
          selected:'',
      }
  },
  mounted(){
    this.$nextTick(() => {
        this.bodyheight=window.innerHeight-48;
    })
    window.ysdt._this=this;
    if(this.data.isshow===1){
        window.ysdt.PositionPicker(this);
    }else{
        this.$nextTick(() => {
            this.$refs.input.focus()
        })
    }
  },
  methods:{
      onsubmit(key){
        if(key){
        this.$emit('getinfo',{type:1,data:{url:this.data.url,data:key}});
        }
      },
      onsearch(){
         window.ysdt.getsearch(this.data.city,this.data.keyword).then((e) => {
            this.data.isshow=0;
            this.data.search=e;
         });
      },
      getnamedata(key){
        this.selected=key;
      },
      nogeolocation(){
        if(this.data.origin){
          this.getnamedata(data);
        }
      },
      getgeolocation(key){
        this.selected=key;
      }
  }
}
</script>

