<template>
  <div v-if="inshow && show.user">
    <div class="position-fixed top-0 start-0 end-0 bottom-0 bg-dark " style="opacity: .5;" @click="onsubmit" ></div>
    <div class="position-fixed top-50 start-50 translate-middle bg-white shadow-sm p-2 rounded fs-normal" style="width: 85%;">
      <div class="d-flex justify-content-between px-2">
        <div class="bd-highlight"></div>
        <div class="bd-highlight"  @click="onsubmit" ><span class="bi bi-x fs-1"></span></div>
      </div>
      <div class="text-center p-2">
        <div>
          <com-avatar :i="show.user.avatar" width="60" height="60" />
        </div>
        <div class="p-2">{{show.user.nickname}} <span class="bi bi-star-fill text-warning"></span> {{show.user.evaluate|toFixed}}</div>
        <div class="p-1 fs-6 fw-bold">{{show.msg}}</div>
        <div class="text-black-50" v-if="show.createdate">操作时间{{show.createdate|setdate}}</div>
      </div>
      <div class="bg-light p-3">
        <div><span class="fw-bold">我的行程</span> <span class="text-black-50">{{show.trip.setdate|setdate(show.trip.setdate_end)}}</span></div>
        <div class="ymerge">{{show.trip.origin}}</div>
        <div class="ymerge">{{show.trip.destination}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  props: {
    show:{},
  },
  data(){
    return {
      inshow:true,
      loading:false,
    }
  },
  methods:{
    onsubmit(){
        this.inshow=false;
        if(this.loading===false){
            this.loading=true;
            axios({method: 'post',url: this.$store.state.apiset.url+'/index/promptdel',params:{token:this.$store.state.token},data:{}}).then(response => {
                this.loading=false;
                setTimeout(() => { this.inshow=true;},2000);
                if (this.$store.state.webrefresh === 1) { this.$store.commit('set_webrefresh', 0);setTimeout(() => {this.$store.commit('set_webrefresh',1);},20000);}
            }).catch(function (error) {this.loading=false;console.log(error)});
        }
    },
  }
}
</script>

<style>

</style>


