<template>
  <modal-center title="取消行程" @onclose="$emit('getinfo',{type:0})">
    <div v-if="data.binding=='p'">
        <div class="p-2">
            <div class="fw-bold py-2 fs-5">确认取消吗？</div>  
            <div class="text-black-50">
                <div class="pt-1 text-rounded d-flex align-items-center">当前取消会影响你的信任分和取消次数</div>
                <div class="pt-1 text-rounded d-flex align-items-center">今天还剩<span class="text-danger">{{data.cancel}}次</span>取消机会</div>
            </div>
        </div>
        <div class="container-fluid p-2">
            <div class="row gx-2">
                <div class="col d-grid">
                    <button class=" btn btn-white border text-primary"  @click="$emit('getinfo',{type:1,data:data.form})">确认取消</button>
                </div>
                <div class="col d-grid">
                    <button class=" btn btn-primary"  @click="$emit('getinfo',{type:0})">暂不取消</button>
                </div>
            </div>
        </div>
    </div>
    <div v-else-if="data.binding=='d'">
        <div class="p-2">
            <div class="fw-bold py-2 fs-5">确认取消吗？</div>  
            <div class="text-black-50">
                <div class="pt-1 text-rounded d-flex align-items-center">当前取消会影响你的信任分和取消次数</div>
                <div class="pt-1 text-rounded d-flex align-items-center">今天还剩<span class="text-danger">{{data.dcancel}}次</span>取消机会</div>
            </div>
        </div>
        <div class="container-fluid p-2">
            <div class="row gx-2">
                <div class="col d-grid">
                    <button class=" btn btn-white border text-primary"  @click="$emit('getinfo',{type:1,data:data.form})">确认取消</button>
                </div>
                <div class="col d-grid">
                    <button class=" btn btn-primary"  @click="$emit('getinfo',{type:0})">暂不取消</button>
                </div>
            </div>
        </div>
    </div>
</modal-center>
</template>
<script>
export default {
  props:{
    data:{}
  }
}
</script>

<style>

</style>


