<template>
    <div class="text-center" v-if="new Date(show.setdate_end).getTime()-new Date().getTime()<15*60*1000">
      <div class="fs-5 fw-bold" v-if="set.title" v-html="set.title"></div>
      <div class="text-black-50 fs-sm p-1" v-if="set.text"  v-html="set.text"></div>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
    show:{},
  },
}
</script>

