<template>
  <div class="bg-white rounded p-3 position-relative fs-normal text-dark">
    <div class="fw-bold stretched-link text-rounded" @click="$emit('geturl','id',data.id)">
      <span class="ybefore">{{data.setdate|setdate(data.setdate_time)}}</span>
      <span class="ybefore">{{data.people}}人</span>
      <span class="ybefore">{{data.carpooling==='1'?'愿拼车':'不拼车'}}</span>
      <span class="badge rounded-pill bg-info">顺风车</span>
    </div>
    <div class="d-flex justify-content-between py-2">
      <div class="bd-highlight">
        <div class="ymerge">{{data.origin}}<span v-if="data.origin_distance>-1" class="ms-2 text-black-50">{{data.origin_distance}}km</span></div>
        <div class="ymerge">{{data.destination}}<span v-if="data.destination_distance>-1" class="ms-2 text-black-50">{{data.destination_distance}}km</span></div>
      </div>
      <div class="bd-highlight text-center">
        <div v-if="data.carpooling==='1'">
          <span class="text-dark fs-4">{{data.n_price}}</span>元
          <div>拼成 <span class="text-danger">{{data.p_price}}</span>元</div>
        </div>
        <div v-else>
          <span class="text-dark fs-4">{{data.price}}</span>元
        </div>
      </div>
    </div>
    <div class="d-flex justify-content-between">
        <div class="bd-highlight d-flex flex-row align-items-center">
          <div class="bd-highlight position-relative stretched-link"  @click="$emit('geturl','user_id',data.user_id)">
            <avatar :i="data.avatar" width="25" height="25" />
          </div>
          <div class="bd-highlight position-relative stretched-link ms-1"  @click="$emit('geturl','user_id',data.user_id)">
            {{data.nickname}}
          </div>
          <div class="bd-highlight ms-2"><span class="bi bi-star-fill text-warning"></span></div>
          <div class="bd-highlight ms-1">{{data.pevaluate}}</div>
          <div class="bd-highlight ms-1">-</div>
          <div class="bd-highlight ms-1">出行 <span class="text-primary">{{data.pint}}</span>次</div>
        </div>
        <div class="bd-highlight text-black-50" v-if="data.ifpay===1">
          已支付
        </div>
    </div>
  </div>
</template>

<script>
import Userlogo from "../icons/user-logo";
export default {
  components: {
    Userlogo,
  },
  props: {
    set:{},
    data:{},
  },
}
</script>



