<template>
  <div>
    <div class="position-fixed top-0 start-0 end-0 bottom-0 bg-dark" style="opacity: .5;" @click="$emit('onclose')"></div>
    <div  class="position-fixed top-50 start-50 translate-middle " style="width: 85%;">
        <div class="bg-white shadow-sm rounded fs-normal">
            <div class="bg-info rounded-top">
                <div class="d-flex justify-content-between align-items-center p-2 px-3">
                    <div class="bd-highlight fs-6 fw-bold">{{title}}</div>
                    <div class="bd-highlight"  @click="$emit('onclose')"><button type="button" class="btn-close"></button></div>
                </div>
            </div>
            <div class="overflow-auto p-2"><slot></slot></div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  props:{
    title:'',
    w:'',
    h:'',
    c:'',
  },
}
</script>


