<template>
    <div class="shadow-sm rounded bg-white m-2  p-2 ">
      <com-form :data="set.form" :form="form" @getinfo="getinfo"/>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      form:{},
    }
  },
  created(){
    this.form=this.set.data;
  },
  methods:{
      onvalidate(){
          for(let x in this.form){
            if(this.form[x]==="" || this.form[x].length===0){
              if(this.set.form[x]){
                if(this.set.form[x].validate){
                return 0;
                }
              }
            }
          }
          return 1;
      },
      getinfo(){
        if(this.onvalidate()===1){
          this.$tourl('driver_edit',this.form);
        }
      }
  }
}
</script>

<style>

</style>


