<template>
  <toast-info class="container-fluid fs-normal" :data="info[binding][show.state]">
    <toast-button :data="{type:'tab',state:1,name:'导航',bi:'cursor-fill',toast:{type:'select',data:[{type:'amap',name:'高德地图'},{type:'baidu',name:'百度地图'}]}}" @run_toast="run_toast"/>
  </toast-info>
</template>

<script>
export default {
  props: {
    binding:'',
    data:{},
    show:{},
  },
  data(){
    return {
       info:{
         "p":{
           "2":{"title":"车主已接单,请尽快<span class='text-primary'>确认</span>","text":"确认后车主才来接"},
           "22":{"title":"已确认同行","text":"请如约出行，有责取消将影响取消次数和信任分"},
           "31":{"title":"司机已到达起点","text":"请如约出行，有责取消将影响取消次数和信任分"},
           "32":{"title":"<span class='text-black-50'>去</span>"+this.show.destination,"text":"请如约出行，有责取消将影响取消次数和信任分,"+(this.show.ispay>0?'<span class="text-primary">已支付</span>':'<span class="text-primary">未支付</span>')},
           "33":{"title":"<span class='text-black-50'>去</span>"+this.show.destination,"text":"请如约出行，有责取消将影响取消次数和信任分,"+(this.show.ispay>0?'<span class="text-primary">已支付</span>':'<span class="text-primary">未支付</span>')},
           "41":{"title":"<span class='text-black-50'>去</span>"+this.show.destination,"text":"司机已确认上车,"+(this.show.ispay>0?'<span class="text-primary">已支付</span>':'<span class="text-primary">未支付</span>')},
           "42":{"title":"<span class='text-black-50'>去</span>"+this.show.destination,"text":"已确认上车,"+(this.show.ispay>0?'<span class="text-primary">已支付</span>':'<span class="text-primary">未支付</span>')},
           "43":{"title":"<span class='text-black-50'>去</span>"+this.show.destination,"text":"行程已开始,"+(this.show.ispay>0?'<span class="text-primary">已支付</span>':'<span class="text-primary">未支付</span>')},
           "51":{"title":"行程已结束","text":"感谢您使用,"+(this.show.ispay>0?'<span class="text-primary">已支付</span>':'<span class="text-primary">未支付</span>')},
           "52":{"title":"行程已结束","text":"感谢您使用,"+(this.show.ispay>0?'<span class="text-primary">已支付</span>':'<span class="text-primary">未支付</span>')},
           "53":{"title":"行程已结束","text":"感谢您使用,"+(this.show.ispay>0?'<span class="text-primary">已支付</span>':'<span class="text-primary">未支付</span>')},
         }
       }
    }
  },
  methods:{
    run_toast(key){
      if(key.type==='amap'){
        this.$toast({type:'tips',data:{text:key.name}});
        this.callMap(this.show);
      }else if(key.type==='baidu'){
        this.$toast({type:'tips',data:{text:key.name}});
      }
    },
    ToggleAppAndH5( AppUrl , AppCallback = () => {}){
        // 先走APP
      const ifr = document.createElement('iframe');
        ifr.src = AppUrl;
        ifr.style.display = 'none';
        document.body.appendChild(ifr);
        setTimeout(function(){
            document.body.removeChild(ifr);
        }, 3000);
          // 800毫秒后调用H5链接
        let timer = setTimeout(function () {
                clearTimeout(timer);
                AppCallback();
        }, 800);
        window.onblur = function () {
            clearInterval(timer);
        };
    },
    Callback(key){
       frameDom.attr('src', "https://ditu.amap.com/dir?type=car&from[lnglat]="+ key.origin_lng +","+ ey.origin_lat +"&from[name]="+key.origin+"&to[lnglat]="+ key.destination_lng +","+ key.destination_lat +"&to[name]="+key.destination+"&src=com.youbei.chefu");
    },
    callMap(key) {
      const u = navigator.userAgent;
      const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
      let proto = isiOS ? 'iosamap://path' : 'amapuri://route/plan'  ;
      const AppUrl = proto + "?t= 0&slat="+key.origin_lat+"&slon="+key.origin_lng+"&sname="+key.origin+"&dlat="+key.destination_lat+"&dlon="+key.destination_lng+"&dname="+key.destination+"&src=xxx";
      this.ToggleAppAndH5(AppUrl,this.Callback(key))
    },
  }
}
</script>

<style>

</style>


