<template>
   <component :is="type" :set="set" :data="data" :show="data.user?data.user:data.userinfo"></component>
</template>

<script>
import viewshow from './user/show';
export default {
  components: { 
      viewshow,
  },
  props: {
    set:{},
    data:{},
  },
  data(){
      return{
          type:'viewshow',
      }
  }
}
</script>

<style>

</style>


