<template>
  <component :is="data.type" 
  :name="name" 
  :data="data"
  :form="form"
  @getrefresh="getrefresh"
  @getset="getset"
  @getenum="getenum"
  @getform="getform"
  @onsubmit="onsubmit"
  />
</template>

<script>
import Viewtext from './i-text';
import Viewpassword from './i-password';
import mobileValidate from './mobile-validate';


import enumCarpooling from './enum-carpooling';
import enumUnit from './enum-unit';
import enumEldate from './enum-eldate';
import enumSetdate from './enum-setdate';
import enumAmapSearch from './enum-amap-search';

import enumAvatar from './enum-avatar';
import enumText from './enum-text';
import enumSubmit from './enum-submit';


import setMessage from './set-message';
import amapSelect from './amap-select';
export default {
  components: { 
    Viewtext,
    Viewpassword,

    mobileValidate,

    enumCarpooling,
    enumUnit,
    enumEldate,
    enumSetdate,
    enumAmapSearch,

    enumAvatar,
    enumText,
    enumSubmit,

    setMessage,
    amapSelect,
  },
  props: {
    name:"",
    data:{},
    form:{},
  },
  created(){
      if(!this.data.selected){
        this.getrefresh(this.name);
      }
  },
  methods:{
      getrefresh(name){
        this.data.selected="";
        if(this.form[name].constructor===Array){
        this.data.selected=Array(); 
        }else{
        this.data.selected={}; 
        }
        for(let x in this.data.selectdd){
          if(this.form[name].constructor===Array){
            if(this.form[name].includes(this.data.selectdd[x].value)){
            this.data.selected.push(this.data.selectdd[x]);
            }
          }else{
            if(this.data.selectdd[x].value===this.form[name]){
            this.data.selected=this.data.selectdd[x];
            }
          }
        }
      },
      getset(name,key){
        if(this.form[name].constructor!==Array){
                this.form[name]=this.form[name]?Array(this.form[name]):Array();
        }
        //重新渲染
        if(this.form[name].includes(key)){
            this.form[name]=this.form[name].filter(function (ele){return ele != key;});
        }else{
            this.form[name].push(key);
        }
        this.getrefresh(name);
      },
      getenum(name,key){
        this.form[name]=key; 
        this.getrefresh(name);
        
      },
      getform(){
      this.$emit('getform');
      },
      onsubmit(key){
       this.$emit('onsubmit',key);
      }
  }
}
</script>

<style>

</style>


