<template>
    <div class="py-2">
        <div class="input-group border-bottom border-primary rounded">
            <span class="input-group-text border-0 bg-primary text-white">+86</span>
            <input type="text" class="form-control  border-0" :class="{'border-danger':data.validate}" :placeholder="data.placeholder" v-model="form[name]" @keyup="onvalue"/>
        </div>
        <div class="input-group mt-3 border-bottom border-primary rounded">
            <span class="input-group-text  border-0  bg-primary  text-white">
                <span class="bi bi-pencil-square"></span>
            </span>
            <input type="text" class="form-control  border-0" v-model="form['captcha']" />
            <button class="btn" :class="submit_loading?'btn-dark':'btn-primary'" type="button" @click="onsubmit" :disabled="submit_loading">{{submitname}}{{submit_number>0?'('+submit_number+')':''}}</button>
        </div>
        <div v-if="loading" class="position-fixed top-0 start-0 end-0 bottom-0 ">
            <div class="position-absolute top-50 start-50 translate-middle"><span class="spinner-border"></span></div>
        </div>
    </div>  
</template>

<script>
import axios from 'axios'
export default {
  props: {
    name:"",
    data:{},
    form:{},
  },
  data(){
      return {
        loading:false,
        submitname:'发送验证码',
        submit_loading:false,
        submit_number:0,
        timer: '',
      }
  },
  methods:{
    onvalue(){
      this.$emit('getform');
      if(typeof this.data.validate!=='undefined'){
          this.data.validate=false
      }
    },   
    getnumber(){
        if(this.submit_number <= 0){
            this.submit_loading=false;
            clearInterval(this.timer);
        }else{
            this.submit_number--
        }
    },  
    onsubmit(){
        if(this.submit_loading===false){
            this.submit_loading=true;
            this.loading=true;
            axios({method: 'post',url: this.$store.state.apiset.url+this.data.accept,data:{mobile:this.form[this.name]}}).then(response => {
                this.loading=false;
                let res = response.data;
                if (res.code===201) {
                    this.submit_number=60;
                    this.submitname = '重新发送';
                    this.timer = setInterval(this.getnumber,1000);
                    this.$toast({type:'result',data:res});
                }else{
                   this.loading=false;
                   this.submit_loading=false;
                   this.$toast({type:'result',data:res});
                }
            }).catch(function (error) {this.loading=false;console.log(error)});
        }
    },
  }
}
</script>

<style>

</style>


