<template>
  <div>
      <com-layout-title :title="set.title" :data="{type:'select',name:'取消订单',data:setdata(this.data.show.id)}" @onsubmit="onsubmit"/>
      <div class="d-flex justify-content-between align-items-center bg-info bg-gradient text-white fs-normal p-2" v-if="data.show">
        <div class="bd-highlight">
          <div class="fs-normal">{{data.show.setdate|setdate(data.show.setdate_end)}} | {{data.show.state|tripstate}}</div>
          <div>
            <span>{{data.show.origin}}</span>
            <span class="bi bi-arrow-right-short"></span>
            <span>{{data.show.destination}}</span>
          </div>
        </div>
        <div class="bd-highlight">
          <span class="fs-5">{{data.show.price}}</span>
          <span>元</span>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
  },
  methods:{
    onsubmit(key){
       this.$emit('onsubmit',key);
    },
    setdata(id){
      return [{name:'重新发布行程',url:'/passenger/state',data:{id:id,state:8}},{name:'取消当前行程',url:'/passenger/state',data:{id:id,state:8}}]
    }
  }
}
</script>


