<template>
    <Layout :head_class="'bg-light'" :main_class="'bg-light'" :foot_class="'bg-white'" >
        <template slot="main">
            <div class="position-fixed start-0 end-0 top-0">
                <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="item.data" />
            </div>
            <div class="position-relative">
                <div class="position-absolute start-0 end-0 bottom-0">
                    <div class="shadow rounded bg-info bg-gradient m-2">
                       <div v-if="!$store.state.token" class="d-flex justify-content-between align-items-center p-2  text-white">
                            <div class="d-flex align-items-center">
                                <div class="d-inline-block">未登录</div>
                            </div>
                            <div>
                                <button class="btn btn-warning" @click="$tourl('login')">登录</button>
                            </div>
                        </div>
                        <div v-if="trip && trip.length===1">
                            <div v-for="(item,key) in trip" :key="key" class="d-flex justify-content-between align-items-center text-white p-2" @click="$tourl('trip-'+item.id)">
                                <div class="bd-highlight">
                                    <div class="fs-6">{{item.state|tripstate}}</div>
                                    <div class="fs-normal">
                                        <span>{{item.setdate|setdate(item.setdate_end)}}</span>
                                        <span class="inbefore">{{item.origin}}</span><span class="inbefore">{{item.destination}}</span>
                                    </div>
                                </div>
                                <div class="bd-highlight"><span class="fs-4 bi bi-chevron-right"></span></div>
                            </div>
                        </div>
                        <div v-else-if="trip && trip.length>1" class="d-flex justify-content-between align-items-center text-white p-2" @click="toast(trip)">
                            <div class="bd-highlight">
                                <div class="fs-6">你有{{trip.length}}个进行的行程</div>
                            </div>
                            <div class="bd-highlight"><span class="fs-4 bi bi-chevron-right"></span></div>
                        </div>
                        <div class="rounded bg-white py-3 px-2">
                            <com-form class="row gy-2" :data="set.form" :form="form" @getinfo="getinfo"/>
                        </div>
                    </div>
                </div>
                <div class="position-absolute start-0 end-0 top-0">
                      <View-group v-for="(item,key) in group" :key="key" :set="item.set" :type="item.type" :data="data" />
                </div>
            </div>
        </template>
    </Layout>
</template>

<script>
import Layout from './public-amap'
import ViewGroup from '../group/start'
export default {
  components: {
    Layout,
    ViewGroup,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  created(){
          this.$root.$on('setuserstate',(msg)=>{ 
          if(msg==='ok'){
            if(this.$store.state.status==='success'){
                this.trip = Object.assign({});
                this.$todd(this.$store.state.userinfo.trip).then(data => {
                    this.trip = data;
                     this.cacheurl();
                });
            }else{
                this.$tourl('login');  
            }
          }
        });
        this.cacheurl();
  },
  data(){
    return {
        trip:this.$store.state.userinfo.trip,
        form:{"origin": "","origin_lat": "","origin_lng": "","destination": "","destination_lat": "","destination_lng": ""},
    }
  },
  methods:{
      cacheurl(){
         for(let x in this.trip){
            this.$emit('onsubmit',{cacheurl:'trip-'+this.trip[x].id});
        }
      },
      toast(list){
         this.$toast({type:'block',data:{set:{body_class:'container-fluid',layout_class:'row gy-2',inner_type:'p-list',url:{id:'trip-'}},data:list}}).then((e) => {
             this.$tourl(e);
         });
      },
      getinfo(key){
         if(key.origin && key.destination){
             this.$tourl('passenger_edit',key);
         }
      }
  }
}
</script>
<style scoped>
.inbefore::after{
  content: "-";
  margin:0px 2px;

}
.inbefore+.inbefore::after {
     display:none;
}
</style>


