<template>
  <div>
      <div @click="data.isActive=true">{{form[name].length?'已有'+form[name].length+'条':data.placeholder}}</div>
      <LayoutFrom :title="data.title" :text="data.text" :isActive="data.isActive" @isActive="onActive">
          <div class="container-fluid">
            <div class="row row-cols-2 g-2">
                <div class="col" v-for="(item,key) in data.selectdd" :key="key" @click="onselect(item.value)">
                    <div class="text-center border rounded p-1 " :class="selected.includes(item.value)?'border-info text-info':'text-black-50'">
                            {{item.name}}
                    </div>
                </div>
            </div>
          </div>
          <div class="d-grid p-2">
                <button class=" btn btn-primary" @click="onsubmit">确定</button>
          </div>
      </LayoutFrom>
   </div>  
</template>

<script>
import LayoutFrom from './layout-bottom';
export default {
  components: { 
    LayoutFrom,
  },
  props: {
    name:"",
    data:{},
    form:{},
  },
  data(){
      return {
         selected:[],
      }
  },
  created(){
      if(this.form[this.name].constructor!==Array){
        this.form[this.name]=this.form[this.name]?Array(this.form[this.name]):Array();
      }
      this.$set(this,"selected",this.form[this.name]);
  },
  methods:{
      onActive(){
        this.data.isActive=false;
      },    
      onsubmit(){
        this.form[this.name]=this.selected
        this.$emit('getrefresh',this.name)
        this.onActive();
      },
      onselect(key){
        //重新渲染
        if(this.selected.includes(key)){
            this.selected=this.selected.filter(function (ele){return ele != key;});
        }else{
            this.selected.push(key);
        }
      }
  }
}
</script>




