<template>
  <div class="app-toast">
    <div class="bg-white shadow-sm rounded-2 p-2 text-dark" style="transform: translate(-50%, -50%);">
      <div class="text-center fw-bold">温馨提示</div>
      <div class="p-2 text-center">{{data.text}}</div>
      <div class="d-grid"><button class=" btn btn-primary" @click="isShow=false">关闭</button></div>
    </div>
   
  </div>
</template>
<script>
export default {
  props:{
    data:{}
  },
}
</script>

<style>

</style>


