<template>
   <div class="position-fixed top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center text-center">
    <div class="position-fixed top-0 start-0"><span class="fs-5 bi bi-chevron-left m-2"  @click="$tourl('../../')"></span></div>
    <div class="bd-highlight">
      <h1 class="fw-light">{{set.title}}</h1>
      <p class="lead text-muted">{{set.text}}</p>
      <div class="px-3">
        <com-form :data="set.form" :form="form" @onsubmit="onsubmit"/>
        <div class="p-2 px-3 fs-normal text-muted">
          <div class="form-check text-start">
            <input class="form-check-input" type="checkbox" value="" id="flexCheckChecked" v-model="tyok">
            <label class="form-check-label" for="flexCheckChecked">
              同意《联通统一认证服务条款》《法律条款与隐私政策》并授权海大出行取本机号
            </label>
          </div>
        </div>
        <div class="d-grid">
              <button class=" btn btn-primary" @click="exsubmit" :disabled="tyok?false:true">确定</button>
        </div>
       </div>
    </div>
  </div>
</template>


<script>
export default {
  components: {
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      form:{
        "mobile": "",
        "captcha": ""
    },
      tyok:false,
    }
  },
  methods:{
    onvalidate(){
        for(let x in this.form){
          if(this.form[x]==="" || this.form[x].length===0){
            if(typeof this.set.form[x]!=='undefined'){
              if(typeof this.set.form[x].validate!=='undefined'){
              this.set.form[x].validate=true
              return 0;
              }
            }
          }
        }
        return 1;
    },
    exsubmit(){
      if(this.onvalidate()===1){
        this.$emit('onsubmit',{method: 'post',url:this.set.formurl,data:this.form});
      }
    },
    onsubmit(key){
        this.$emit('onsubmit',key);
    }
  }
}
</script>

<style>

</style>


