<template>
  <div :class="layout_class">
    <div ref="head" :class="head_class">
        <slot name="head"></slot>
    </div>
    <div class="overflow-auto" :class="main_class" :style="{height:bodyheight+'px'}">
        <slot name="main"></slot>
    </div>
    <div ref="foot" :class="foot_class">
        <slot name="foot"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props:{
    layout_class:'',
    head_class:'',
    main_class:'',
    foot_class:'',
  },
  data(){
        return {
            bodyheight:0,
            headheight:0,
            footheight:0,
        }
  },
  mounted(){
    this.headheight=this.$refs.head.offsetHeight;
    this.footheight=this.$refs.foot.offsetHeight;
    this.bodyheight=window.innerHeight-this.headheight-this.footheight;
    const _this = this;
    // 监听浏览器窗口变化
    window.onresize = function() {
      return (() => {
        _this.bodyheight = window.innerHeight-_this.headheight-_this.footheight;
      })();
    };
  }
}
</script>


