<template>
  <component :is="type" :set="set" :data="data" @onsubmit="onsubmit"></component>
</template>

<script>
import formNone from './form-none'
import listK1 from './list-k1'
import listK2 from './list-k2'
import listK3 from './list-k3'
import headInfo from './head-info'
import headNav from './head-nav'
import headUser from './head-user'
import infoUser from './info-user'

import editInfo from './edit-info'


import pHeadInfo from './p-head-info'
import dHeadInfo from './d-head-info'
import routeHeadInfo from './route-head-info'


import chatHead from './chat-head'
export default {
  components: {
    formNone,
    listK1,
    listK2,
    listK3,
    routeHeadInfo,
    pHeadInfo,
    dHeadInfo,
    headInfo,
    headNav,
    headUser,
    infoUser,
    editInfo,

    chatHead,
  },
  props: {
    type:'',
    set:{},
    data:{},
  },
  methods:{
      onsubmit(key){
         this.$emit('onsubmit',key);
      },
  }
}
</script>

<style>

</style>


