<template>
  <div class="py-2">
      <toast-nav :data="setdata(show.state)" @run_toast="run_toast"/>
  </div>
</template>

<script>
export default {
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  },
  data(){
    return {
      toastnav:[
        {type:'border',state:1,name:'改手机号',bi:'tablet-fill',tourl:'mymobile'},
        {type:'border',state:1,name:'客服中心',bi:'bookmark-x-fill',tourl:'consult'},
        {type:'border',state:1,name:'取消订单',bi:'bookmark-x-fill',stateinfo:{1:{toast:{type:'cancel-trip'}}}},
      ],
      info:{
        "p":{
          "cancel":['1','2','22','31','32','33'],
          "cancel_data":{url:'/passenger/state',data:{id:this.show.id,state:'8'}},
        },
        "d":{
          "cancel":['2','22','31','32','33'],
          "cancel_data":{url:'/driver/state',data:{id:this.show.id,state:'7'}},
        },
      }
    }
  },
  methods:{
    run_toast(key){
        this.$emit('onsubmit',{method:'post',url:key.url,data:key.data});
    },
    setdata(state){
      let data=this.toastnav;
      if(this.info[this.binding].cancel.includes(state)){
        data[2].state=1;
      }else{
        data[2].state=0;
      }
      data[2].stateinfo[1].toast.data={binding:this.binding,cancel:this.$store.state.userinfo.cancel_int,dcancel:this.$store.state.userinfo.dcancel_int,form:this.info[this.binding].cancel_data};
      return data;
    }
  }
}
</script>

<style>

</style>


