<template>
<div>{{form[name]}}</div>
</template>

<script>
export default {
  props: {
    name:"",
    data:{},
    form:{},
  },
  data(){
      return {
          selected:'',
        bodyheight:0,
        headheight:48,
        isActive:false,
        }
  },
  mounted(){
       ysdt._this=this;
       ysdt.PositionPicker(this).then(e=>{
         console.log(e);
       })
  },
  methods:{
      getnamedata(key){
        this.form[this.name]=key.value;
        this.data.selected=key;
        console.log(key);

      }
  }
}
</script>

