<template>
<div class="container-fluid">
    <div class="bg-white row align-items-center p-1">
        <div class="col-auto" @click="$tourl('../')">
            <span class="fs-4 bi bi-chevron-left"></span>
        </div>
        <div class="col">
            <div class="pt-1 text-center fs-5">{{title}}</div>
        </div>
        <div class="col-auto">
            <com-onselect v-if="data" :type="data.type" :name="data.name" :data="data.data" @onsubmit="onsubmit"  class="text-black-50 fs-normal">取消行程</com-onselect>
        </div>
    </div>
</div>
</template>

<script>
import Onselect from './onselect'
export default {
  components: {
    Onselect,
  },
  props:{
    title:'',
    data:'',
  },
  methods:{
    onsubmit(key){
      this.$emit('onsubmit',key);
    },
  }
}
</script>


