<template>
  <div>
      <div @click="data.isActive=true">{{form[name]?form[name]+data.unit:data.placeholder}}</div>
      <LayoutFrom :title="data.title" :text="data.text" :isActive="data.isActive" @isActive="onActive">
          <div class="container-fluid py-2">
            <div class="row row-cols-4 g-2">
                <div class="col" v-for="(item,key) in data.selectdd" :key="key" @click="selected=item===selected?'':item">
                <div class="text-center border rounded py-4" :class="{'border-info text-info':item===selected,'text-black-50':item!==selected}">
                     <span class="fs-1" :class="{'text-info':item===selected,'text-dark':item!==selected}">{{item}}</span>{{data.unit}}
                </div>
                </div>
            </div>
          </div>
          <div class="d-grid p-2">
                <button class=" btn btn-primary" @click="onsubmit">确定</button>
          </div>
       </LayoutFrom>
   </div>  
</template>

<script>
import LayoutFrom from './layout-bottom';
export default {
  components: { 
    LayoutFrom,
  },
  props: {
    name:"",
    data:{},
    form:{},
  },
  data(){
      return {
          selected:'',
      }
  },
  created(){
    this.$set(this,"selected",this.form[this.name]);
  },
  methods:{
      onActive(){
        this.data.isActive=false;
      },    
      onsubmit(){
        this.form[this.name]=this.selected
        this.$emit('getrefresh',this.name)
        this.$emit('getform',this.name,this.form[this.name]);
        this.onActive();
      },
  }
}
</script>

<style>

</style>


