<template>
    <div class="d-grid" v-if="info[binding][show.state]">
        <button v-if="info[binding][show.state].url" class=" btn btn-primary" @click="$emit('onsubid',info[binding][show.state])">{{info[binding][show.state].name}}</button>
        <button v-else class=" btn btn-dark" disabled>{{info[binding][show.state].name}}</button>
    </div>
</template>

<script>
export default {
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  },
  data(){
    return {
      info:{
        "p":{
          "1":{ "name": "邀请同行" },
          "2":{ "name": "确认同行", "url": "/passenger/state", "data": { "state": "2" } },
          "22":{ "name": "确认到达起点", "url": "/passenger/state", "data": { "state": "3" } },
          "31":{ "name": "确认到达起点", "url": "/passenger/state", "data": { "state": "3" } },
          "32":{ "name": "确认上车", "url": "/passenger/state", "data": { "state": "4" } },
          "33":{ "name": "确认上车", "url": "/passenger/state", "data": { "state": "4" } },
          "41":{ "name": "确认上车", "url": "/passenger/state", "data": { "state": "4" } },
          "42":{ "name": "到达目的地", "url": "/passenger/state", "data": { "state": "5" } },
          "43":{ "name": "到达目的地", "url": "/passenger/state", "data": { "state": "5" } },
          "51":{ "name": "到达目的地", "url": "/passenger/state", "data": { "state": "5" } },
          "52":{ "name": "已确认到达" },
          "53":{ "name": "行程已结束" },
        },
        "d":{
          "1":{ "name": "确认同行", "url": "/driver/tongxin", "data": { "state": "2" } },
          "2":{ "name": "待乘客确认同行"},
          "22":{ "name": "到达起点", "url": "/driver/state", "data": { "state": "3" } },
          "31":{ "name": "确认上车", "url": "/driver/state", "data": { "state": "4" } },
          "32":{ "name": "到达起点", "url": "/driver/state", "data": { "state": "3" } },
          "33":{ "name": "确认上车", "url": "/driver/state", "data": { "state": "4" } },
          "41":{ "name": "到达目的地", "url": "/driver/state", "data": { "state": "5" } },
          "42":{ "name": "确认上车", "url": "/driver/state", "data": { "state": "4" } },
          "43":{ "name": "到达目的地", "url": "/driver/state", "data": { "state": "5" } },
          "51":{ "name": "已确认到达" },
          "52":{ "name": "到达目的地", "url": "/driver/state", "data": { "state": "5" } },
          "53":{ "name": "行程已结束" },
        },
      }
    }
  }
}
</script>

