<template>
  <div class="bg-white p-2 rounded">
    <div class="rounded" :id="'container_'+show.id" style="width:260px;height:150px;"></div>
  </div>
</template>

<script>
export default {
  props: {
    show:{},
  },
  mounted(){
    window.ysdt._this=this;
    window.ysdt.dingwei(this,this.show.id,this.show.data.lng,this.show.data.lat);
  },
}
</script>



