<template>
  <div>
      <com-layout-title :title="data.user.nickname"/>
      <div class="d-flex justify-content-between align-items-center bg-info bg-gradient text-white fs-normal p-2" v-if="data.trip">
        <div class="bd-highlight">
          <div class="fs-normal">{{data.trip.setdate|setdate(data.trip.setdate_end)}} | {{data.trip.state|tripstate}}</div>
          <div>
            <span>{{data.trip.origin}}</span>
            <span class="bi bi-arrow-right-short"></span>
            <span>{{data.trip.destination}}</span>
          </div>
        </div>
        <div class="bd-highlight" v-if="data.push[3]">
            <toast-button :data="setdata(data.trip.state)" @run_toast="run_toast"/>
        </div>
      </div>
      <div class="d-flex justify-content-between align-items-center bg-info bg-gradient text-white fs-normal p-2" v-else-if="data.driver">
        <div class="bd-highlight">
          <div class="fs-normal">{{data.driver.setdate|setdate}} | {{data.driver.state|driverstate}}</div>
          <div>
            <span>{{data.driver.origin}}</span>
            <span class="bi bi-arrow-right-short"></span>
            <span>{{data.driver.destination}}</span>
          </div>
        </div>
        <div class="bd-highlight" v-if="data.push[3]">
        </div>
      </div>
  </div>
</template>

<script>
export default {
  components: { 
  },
  props: {
    set:{},
    data:{},
  },
  data(){
    return {
      toastnav:{type:'tab-dark',state:"0",bi:'telephone-fill',stateinfo:{"0":{toast:{type:'tips',data:{text:'请接单后,才可电话联系'}}},"1":{tel:this.data.user.mobile}}},
      mobile:['2','22','31','32','33','41','42','43','51','52'],
    }
  },
  methods:{
    chatname(){
      let sid,tid
       sid=this.$store.state.userinfo.id;
       tid=this.getid();
      return sid>tid?tid+','+sid:sid+','+tid;
    },
    getid(){
      if(this.binding==='p'){
        if(this.show.state==='1'){
          return this.show.user_id;
        }else{
          return this.show.duser_id;
        }
      }else if(this.binding==='d'){
       return this.show.user_id;
      }
    },
    run_toast(key){
        this.$emit('onsubmit',{method:'post',url:key.url,data:key.data});
    },
    setdata(state){
      let data=this.toastnav;
      data.state=this.mobile.includes(state)?1:0;
      return data;
    }
  }
}
</script>


