<template>
  <div class="py-2" v-if="data[set.list.binding] && data[set.list.binding].length>0">
      <div class="p-2 pt-0 fw-bold fs-6" v-if="set.title">{{set.title}}</div>
      <ViewList :set="set.list.set" :data="data[set.list.binding]"/>
  </div>
</template>

<script>
import ViewList from '../block/start';
export default {
  components: { 
    ViewList,
  },
  props: {
    set:{},
    data:{},
  },
}
</script>

<style>

</style>


