<template>
  <div>
    <div v-if="show.data">
        <Amap v-if="show.data.type==='amap'" :show="show" />
    </div>
    <div v-else class=" bg-info rounded rounded-10 rounded-start border border-info p-2 mx-1">{{show.text}}</div>
  </div>
</template>

<script>

import Amap from './amap'
export default {
  components: {
    Amap,
  },
  props: {
    show:{},
  },
}
</script>



