<template>
  <div>
    <div class="position-relative py-5 px-3">
        <div class="position-absolute start-0 end-0 bottom-0 container-fluid" >
          <Viewinput :name="'carpooling'" :data="data.carpooling" :form="form" @getform="getform" />
        </div>
    </div>
    <div class="d-flex justify-content-around p-2">
        <Viewinput :name="'setdate'" :data="data.setdate" :form="form" @getform="getform" />
    </div>
    <div class="d-flex justify-content-between">
        <div class="bd-highlight p-2">
          <Viewinput :name="'people'" :data="data.people" :form="form" @getform="getform" />
        </div>
        <div class="bd-highlight p-2">
          <Viewinput :name="'thankfee'" :data="data.thankfee" :form="form" @getform="getform" />
        </div>
        <div class="bd-highlight p-2">
          <Viewinput :name="'message'" :data="data.message" :form="form" @getform="getform" />
        </div>
    </div>
  </div>
</template>

<script>
import formdata from './formdata.json';
import Viewinput from './input';
export default {
  components: { 
    Viewinput,
  },
  props: {
    set:{},
    data:{},
    form:{},
  },
  created(){
    //设置数据
    for(let x in this.data){
      if(this.data[x].binding){
        for(let e in formdata[this.data[x].binding]){
            if(!this.data[x][e]){
              this.$set(this.data[x],e,formdata[this.data[x].binding][e]);
            }
        }
      }
    }
  },
  methods:{
      getform(name,value){
        this.$emit('getinfo',this.form);
      },
  }
}
</script>

<style>

</style>


