<template>
    <View-group :set="set" :binding="set.binding==='driver'?'d':'p'" :show="data.show" :data="data"  @onsubmit="onsubmit"/>
</template>

<script>
import ViewGroup from './trip/start'
export default {
  components: {
    ViewGroup,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  mounted(){
  },
  methods:{
      onsubmit(key){
         this.$emit('onsubmit',key);
      },
  }
}
</script>



