<template>
    <div :class="set.body_class?set.body_class:'container-fluid'">
        <div v-if="data && data.length>0" :class="set.layout_class">
            <Inner v-for="(item,index) in data" :key="index" :data="item" :type="set.inner_type" :set="set" @click="getinfo(item)" @getinfo="getinfo"></Inner>
        </div>
        <Empty v-else-if="data.length===0 && set.empty" :data="set.empty" @getinfo="getinfo"></Empty>
    </div>
</template>

<script>
import Inner from './inner/start.js'
import Empty from './empty/start'
export default {
    components:{
        Inner,
        Empty,
    },
    props: {
        set:{},
        data:{}
    },
    created(){
        if(this.data===""){
            this.data=[];
        }
    },
    methods:{
        getinfo(key){
            this.$emit('getinfo',key);
        },
    }
}
</script>



