<template>
    <div class="row row-cols-2 g-3">
        <div class="col" v-for="(item,key) in data.selectdd" :key="key" @click="onselected(item.value)">
            <div v-if="item.value===1" class="bg-white shadow-sm border-bottom border-3 rounded p-4 text-center" :class="item.value===form[name]?'border-primary':'text-black-50'">
                <div class="fw-bold" :class="item.value===form[name]?'text-primary':''">{{item.name}}</div>
                <div class="m-2"><span class="bi bi-person-fill fs-1" :class="item.value===form[name]?'text-info':''"></span></div>
                <div class="fs-normal">拼成人<span class="fs-6 fw-bold" :class="item.value===form[name]?'text-primary':''">{{form.p_price}}</span>{{data.unit}}</div> 
                <div class="fs-normal">未拼成<span :class="item.value===form[name]?'text-primary':''">{{form.n_price}}</span>{{data.unit}}</div>
            </div>
            <div v-else class="bg-white shadow-sm border-bottom border-3 rounded p-4 text-center" :class="item.value===form[name]?'border-primary':'text-black-50'">
                <div class="fw-bold" :class="item.value===form[name]?'text-primary':''">{{item.name}}</div>
                <div class="m-2"><span class="bi bi-people-fill fs-1" :class="item.value===form[name]?'text-info':''"></span></div> 
                <div class="fs-normal">独享价<span class="fs-6 fw-bold text-primary">{{form.price}}</span>{{data.unit}}</div>
                <div class="fs-normal">-</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  components: { 
  },
  props: {
    name:"",
    data:{},
    form:{},
  },
  methods:{
      onselected(value){
        this.form[this.name]=value
      },
  }
}
</script>

<style>

</style>


