<template>
    <div class="my-scroll" ref="myScroll" @touchstart="touchStart($event)" @touchmove="touchMove($event)" @touchend="touchEnd($event)" :style="{height:bodyheight+'px'}">
        <div :style="{height:top+'px'}">
            <div v-if="aspect==2">
                <div class="text-center p-2 fs-normal" v-if="state==6 || state==1 || state==2 || state==3">
                    <div class="d-inline-block text-black-50">
                        <div class="row align-items-center">
                            <!-- <div class="col-auto">
                                <span v-if="state===1" class="fs-2 bi bi-arrow-repeat"></span>
                                <span v-else class="fs-2 bi bi-arrow-up-short"></span>
                            </div> -->
                            <div class="col">
                                <div class="fw-bold fs-6">{{info[state]}}</div>
                                <div>{{info[0]}}{{rdate}}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- top -->
        <div :style="{ transform: 'translate3d(0, ' + top + 'px, 0)'}">
            <slot></slot>
            <div class="text-center" v-if="state==4 || state==5 || state==7">
                {{info[state]}}
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'myScroll',
    props:{
        'page':{
            type:Object,  //counter:当前页  pageStart:开始页数  pageEnd:结束页数  total:总页数
            require:true,
        },
        'onRefresh':{ //刷新回调
            type:Function,
            require:true
        },
        'onPull':{ //加载回调
            type:Function,
            require:true
        },
        'getScrollTop':{ //获取滚动条位置
            type:Function
        },
        'setScrollPage':{ //改变滚动条位置
            type:Function
        },
    },
    data(){
        return {
            pageX:0,
            pageY:0,
            state:0, 
            scrollPosition:0,
            myScroll:null,
            myScrollList:null,
            top:0,
            aspect:0, //1:向下 2:向上
            listHeight:0,
            bodyheight:500,
            rdate:new Date().format("h:mm"),
            info:["上次更新时间 : ","正在刷新..","释放立即刷新","刷新完成","正在加载..","加载完成","下拉可以刷新","没有更多"],
        }
    },
    created(){
        this.$root.$on('setState', (index) => { //修改状态
            this.state = index
            if(index == 5||index == 3){
                setTimeout(()=>{
                    this.state = 0
                    this.top = 0
                },300)
            }
            if(index===3){
                this.rdate=new Date().format("h:mm");
            }
        })
        this.$root.$on('ScrollTop',(top)=>{ //修改滚动条位置
            this.myScroll.scrollTop = top
        })
    },
    methods:{
        touchStart(e){ //触摸事件
            this.pageX = e.targetTouches[0].pageX
            this.pageY = e.targetTouches[0].pageY
        },
        touchMove(e){ //触摸滑动事件
            this.scrollPosition = this.myScroll.scrollTop //获取滚动条位置
            if(e.targetTouches[0].pageY>this.pageY){ //向上滑动
                this.aspect = 2
                if(this.myScroll.scrollTop==0){
                    let diff = e.targetTouches[0].pageY - this.pageY - this.scrollPosition
                    this.top = Math.pow(diff, 0.9)
                    let ranget = diff/document.body.clientHeight*100 //计算在屏幕上滑动了多少
                    if(ranget > 20){
                        this.state = 2
                    }else if(ranget < 15){
                        this.state = 6
                    }
                    e.preventDefault()
                }
            }else if(this.state!=4){ //向上滑动
                this.aspect = 1
            }

        },
        touchEnd(e){
            if(this.aspect == 2&&this.state == 2||this.state == 1){ //上拉处理
                this.top = 100
                this.state = 1
                this.topCallback()
            }else if(this.aspect == 2){
                this.state = 0
                this.top = 0
            }
        },
        topCallback(){ //刷新回调
            this.onRefresh(this.state)
        },
        bottomCallback(){ //加载回调
            this.state = 4
            this.onPull(this.state)
        }
    },
    mounted(){
        this.myScroll = this.$refs.myScroll //获取滑条dom
        this.myScrollList = this.myScroll.children[1] //获取列表dom
        this.bodyheight=window.innerHeight-this.myScroll.offsetTop;
        this.myScroll.addEventListener('scroll',(e)=>{ //监听滚动条事件
            let listHeight = this.myScrollList.offsetHeight //列表总高度
            let listScrollTop = e.target.scrollTop + this.myScroll.offsetHeight //当前滚动条位置

            if(this.state == 0&&listHeight-listScrollTop < 100){
                this.bottomCallback()
            }

            if(this.getScrollTop)this.getScrollTop(e.target.scrollTop) //返回X，Y

        })
    }
}
</script>
<style>
 .my-scroll{
     touch-action: none;
        overflow:hidden;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
        will-change: transform;
        transition: all 450ms;
        backface-visibility: hidden;
        /* perspective: 1000; */
 }

</style>


