<template>
 <com-layout :layout_class="set.layout_class" :head_class="set.head_class" :main_class="set.main_class" :foot_class="set.foot_class">
    <template slot="head">
      <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="data" @onsubmit="onsubmit" />
    </template>
    <template slot="main">
      <Layout-scroll :page="page" :on-refresh="onRefresh" :on-pull="onPull">
        <ViewList :set="set.listset" :data="data[set.listset.data_type].data"/>
      </Layout-scroll>
    </template>
    <template slot="foot">
      <View-group v-for="(item,key) in set.foot" :key="key" :set="item" :type="item.type" :data="item.data" @onsubmit="onsubmit"/>
    </template>
  </com-layout>
</template>


<script>
import LayoutScroll from './layout-scroll'
import ViewList from '../block/start';
import ViewGroup from '../group/start'
export default {
  components: {
    ViewGroup,
    LayoutScroll,
    ViewList,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return{
          page:{
              counter:1,  
              pageStart:1,  
              pageEnd:1,  
              total:1
          },
          loading:true,
      }
  },
  methods:{
    onRefresh(mun){ //刷新回调
        this.page.counter=1;
        this.pathaxios('refresh');
    },
    onPull(mun){ //加载回调
        if(this.page.counter<this.page.total){
              this.page.counter=2;
              this.pathaxios('pull');
        }else{
            this.$root.$emit('setState',7)
        }
    },
    pathaxios(loadkey){
        if(this.loading){
        this.loading=false;
        let pathurl = this.$route.path.split('/').pop();
        this.$http.post(this.$store.state.apiset.url+'/index/layout/class/'+pathurl+'?token='+this.$store.state.token+'&page='+this.page.counter,{msg:'list'})
        .then(response => {
            let res=response.data;
            this.loading=true;
            if(res.code==1){
                this.data[this.set.listset.data_type]=res.data.data[this.set.listset.data_type];
                res=res.data.data[this.set.listset.data_type];
                this.page={
                    counter:res.current_page,
                    pageStart:1,
                    pageEnd:Math.ceil(res.total/res.per_page),
                    total:res.last_page             
                    };
                if(loadkey=="refresh"){
                        this.$root.$emit('setState',3)
                }else if(loadkey=="pull"){
                        for(let index in res.data){
                        this.data[this.set.listset.data_type].push(res.data[index])
                        }
                        this.$root.$emit('setState',5)
                }else{
                        this.data[this.set.listset.data_type]=res.data;
                }
            }else{

            }
        }).catch(function (error) {this.loading=true;console.log('请求失败！')})
        }
    }, 
    onsubmit(key){
       this.$emit('onsubmit',key);
    },
  }
}
</script>



