<template>
  <modal-center title="支付" @onclose="$emit('getinfo',{type:0})">
    <div v-if="data.binding=='p'">
        <div class="alert alert-danger p-2 m-1">
            <div class="d-flex justify-content-between">
                <div class="bd-highlight">系统暂时不支付在线支付</div>
            </div>
        </div>  
        <div class="p-2">
            <div class="text-center">
                <div><span class="fw-bold fs-4">{{data.fee.passenger}}</span>元</div>
                <div class="text-black-50">你的行程费用</div>
            </div>
            <div class="rounded-pill px-5" v-if="data.wximg">
                <img :src="data.wximg" width="100%" />
            </div>
        </div>
        <div class="container-fluid p-2">
            <div class="row gx-2">
                <div class="col d-grid">
                    <button class=" btn btn-white border text-primary" @click="$emit('getinfo',{type:0})">取消</button>
                </div>
                <div class="col d-grid">
                    <button class=" btn" :class="data.toastpay===1?'btn-dark':'btn-primary'"  @click="$emit('getinfo',{type:1,data:data.form})" :disabled="data.toastpay===1?true:false">{{data.toastpay===1?'已提示':'已支付'}}</button>
                </div>
            </div>
        </div>
    </div>
    <div v-else-if="data.binding=='d'">
        <div class="alert alert-danger p-2 m-1">
            <div class="d-flex justify-content-between">
                <div class="bd-highlight">系统暂时不支付在线支付</div>
            </div>
        </div>  
        <div class="text-center p-2">
            <div><span class="fw-bold fs-4">{{data.fee.passenger}}</span>元</div>
            <div class="text-black-50">请向乘客收取行程费用</div>
            <div class="text-black-50">服务费:{{data.fee.service}}元 | 结算：{{data.fee.driver}}元</div>
        </div>
        <div class="container-fluid p-2">
            <div class="row gx-2">
                <div class="col d-grid">
                    <button class=" btn btn-white border text-primary" @click="$emit('getinfo',{type:0})">取消</button>
                </div>
                <div class="col d-grid">
                    <button class=" btn btn-primary"  @click="$emit('getinfo',{type:1,data:data.form})">确认收款</button>
                </div>
            </div>
        </div>
    </div>
</modal-center>
</template>
<script>
export default {
  props:{
    data:{}
  }
}
</script>

<style>

</style>


