<template>
  <div class="bg-white rounded p-2 position-relative fs-normal text-dark ">
    <div class="d-flex justify-content-between border-bottom border-light py-1">
      <div class="bd-highlight  stretched-link"  @click="$emit('geturl','id',data.id)">
        <!-- <span><span class="text-primary fw-bold fs-6">70%</span>顺路度</span> -->
        <span>{{data.setdate|setdate}}</span>
        <span>| 可载{{data.people}}人</span>
        
      </div>
      <div class="bd-highlight">
        <!-- <span class="fw-bold fs-6">17.5</span>元 -->
      </div>
    </div>
    <div class="d-flex justify-content-between py-1  align-items-center">
      <div class="bd-highlight">
        <div class="d-flex flex-row align-items-center">
          <div class="bd-highlight position-relative stretched-link" @click="$tourl('user-'+data.user_id)"><img :src="data.imgurl" width="50" height="50" class="rounded-pill border border-2 border-white"></div>
          <div class="bd-highlight ms-1">
            <div class="fw-bold fs-6 position-relative stretched-link" @click="$tourl('user-'+data.user_id)">{{data.brand}}</div>
            <div><span>{{data.typeinfo}} {{data.colour}}</span><span class="ms-1">{{data.devaluate.toFixed(1)}}分</span><span class="ms-1">接单{{data.dint}}次</span></div>
          </div>
        </div>
      </div>
      <div class="bd-highlight">
        <div class="border border-primary rounded-pill py-1 px-3 text-primary">邀请</div>
      </div>
    </div>
    <!-- <div>
      <div class="d-inline-block me-1 border border-black-50 text-black-50 rounded rounded-20 p-1 px-2 fs-sm">素质较高<span class="ms-1">1</span></div>
      <div class="d-inline-block me-1 border border-black-50 text-black-50 rounded rounded-20 p-1 px-2 fs-sm">素质较高<span class="ms-1">1</span></div>
      <div class="d-inline-block me-1 border border-black-50 text-black-50 rounded rounded-20 p-1 px-2 fs-sm">素质较高<span class="ms-1">1</span></div>
    </div> -->
  </div>
</template>

<script>
import Userlogo from "../icons/user-logo";
export default {
  components: {
    Userlogo,
  },
  props: {
    set:{},
    data:{},
  },
}
</script>



