<template>
   <div v-if="data.type" @click="onclick">
       <button v-if="data.type==='button'" class="btn btn-primary fs-normal"><span v-if="data.bi" class="bi me-1" :class="'bi-'+data.bi"></span>{{data.name}}</button>
       <div v-else-if="data.type==='border'" class="border rounded p-1 text-center" :class="data.state===0?'bg-light text-black-50':'border-black-50 text-dark'"><span v-if="data.bi" class="bi me-1" :class="'bi-'+data.bi"></span>{{data.name}}</div>
       <div v-else-if="data.type==='tab'" class="text-center"><span class="d-inline-block text-center border border-primary rounded-pill bg-white fs-6  mx-1 bi  text-primary bi" :class="'bi-'+data.bi" style="width: 30px; height: 30px;"></span><div v-if="data.name" class="mt-1 fs-sm text-black-50">{{data.name}}</div></div>
       <div v-else-if="data.type==='tab-dark'" class="text-center">
           <span class="d-inline-block text-center border border-black-50 rounded-pill bg-white fs-6  mx-1 bi" :class="'bi-'+data.bi+' '+(data.state===1?'text-dark':'text-black-50')" style="width: 30px; height: 30px;"></span>
           <div v-if="data.name" class="mt-1 fs-sm text-black-50">{{data.name}}</div>
       </div>
       <div v-else-if="data.type==='chat'" class="text-center">
           <span class="position-relative d-inline-block text-center border border-black-50 rounded-pill bg-white fs-6  mx-1 bi" :class="'bi-'+data.bi+' '+(data.state===1?'text-dark':'text-black-50')" style="width: 30px; height: 30px;">
                <span class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger" v-if="data.int && data.int>0">{{data.int}}</span>
           </span>
           <div v-if="data.name" class="mt-1 fs-sm text-black-50">{{data.name}}</div>
        </div>
   </div>
</template>
<script>
export default {
  props:{
    data:{},
  },
  methods:{
      onclick(){
          if(this.data.stateinfo){
              
              this.oninner(this.data.stateinfo[this.data.state]);
          }else{
              this.oninner(this.data);
          }
      },
     oninner(key){
        if(!key) return "";
        if(key.tourl){
            this.$tourl(key.tourl);
        }else if(key.callmap){
            this.callMap(key.callmap);
        }else if(key.tel){
            window.location.href='tel:'+key.tel;
        }else if(key.toast){
            this.$toast(key.toast).then((e) => {
                this.$emit('run_toast',e);
            });
        }
     } 
  }
}
</script>


