<template>
    <div class="position-fixed top-0 start-0 end-0 bottom-0" v-show="isActive" style="z-index:3" >
        <div class="p-2 bg-white position-relative border-bottom">
            <div class="pe-5">
                <div class="input-group input-group-sm">
                    <span class="input-group-text">{{data.city}}</span>
                    <input class="form-control" value="" v-model="data.keyword" ref="input" @keyup="$emit('onsearch',data.city,data.keyword)" :placeholder="data.s_placeholder" />
                </div>
            </div>
            <div class="position-absolute top-0 end-0 p-2"><div @click="$emit('isActive')">取消</div></div>
        </div>
        <div class="bg-light overflow-auto" :style="{height:bodyheight+'px'}">
            <div class="container-fluid">
                <div v-for="(item,index) in data.search" :key="index">
                    <div class="row align-items-center bg-white" @click="$emit('onsubmit',item)">
                        <div class="col-auto">
                            <div class="py-1">
                                <span class="bi fs-5 text-black-50" :class="'bi-'+(item.icons?item.icons:'clock')"></span>   
                            </div>
                        </div>
                        <div class="col p-0 border-bottom ">
                            <div class="d-flex justify-content-between align-items-center">
                                <div class="bd-highlight py-1">
                                    <div>{{item.name}}</div>
                                    <div v-if="item.address" class="text-black-50 fs-normal"><span v-if="item.adname">{{item.adname}}-</span>{{item.address}}</div>
                                </div>
                                <div class="bd-highlight">
                                    
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    data:"",
    isActive:false,
  },
  data(){
      return {
        bodyheight:0,
        headheight:48,
        }
  },
  watch:{
      isActive(){
          if(this.isActive===true){
            document.body.parentNode.style.overflow = "hidden";
            this.$nextTick(() => {
                this.$refs.input.focus()
            })
          }else{
            document.body.parentNode.style.overflow = "auto";
          }
      }
  },
  mounted(){
    this.bodyheight=window.innerHeight-this.headheight;
    const _this = this;
    // 监听浏览器窗口变化
    window.onresize = function() {
      return (() => {
        _this.bodyheight = window.innerHeight-_this.headheight;
      })();
    };
  },
}
</script>



