<template>
  <div>
    <com-layout-title :title="set.title" :data="{type:'select',name:'取消订单',data:setdata(this.data.show.id)}" @onsubmit="onsubmit"/>
    <div class="d-flex justify-content-between align-items-center bg-info bg-gradient text-white fs-normal  p-2">
      <div class="bd-highlight" v-if="data.show">
        <div class="fs-normal">正在匹配行程 | 可载{{data.show.people}}人</div>
        <div><span class="inbefore">{{data.show.origin}}</span><span class="inbefore">{{data.show.destination}}</span></div>
      </div>
      <div class="bd-highlight">
        <span class="fs-3 bi bi-broadcast"></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
  },
  methods:{
    onsubmit(key){
       this.$emit('onsubmit',key);
    },
    setdata(id){
      return [{name:'重新发布行程',url:'/driver/cancel',data:{id:id,state:7}},{name:'取消当前行程',url:'/driver/cancel',data:{id:id,state:7}}]
    }
  }
}
</script>



