<template>
    <div class="d-grid">
        <button class=" btn btn-primary" @click="$tourl(binding==='d'?'driver_edit':'passenger_edit',{origin:show.origin,origin_lng:show.origin_lng,origin_lat:show.origin_lat,destination:show.destination,destination_lng:show.destination_lng,destination_lat:show.destination_lat})">重新发布行程</button>
    </div>
</template>

<script>
export default {
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  }
}
</script>

