function formatname(key) {
    if (key) {
        return key.replace(new Date(new Date().getTime() + 0 * 60 * 60 * 1000).format("yyyy-MM-dd"), '今天').replace(new Date(new Date().getTime() + 24 * 60 * 60 * 1000).format("yyyy-MM-dd"), '明天').replace(new Date(new Date().getTime() + 48 * 60 * 60 * 1000).format("yyyy-MM-dd"), '后天').replace(new Date(key).format("yyyy-MM-dd"), new Date(key).format("M月d日"))
    }
    return key;
}
export function datardf(key) {
    var myDate = key ? new Date(key) : new Date();
    var _mh = myDate.getHours();
    var _mm = myDate.getMinutes();
    _mm = parseInt(_mm / 5) + 2;
    if (_mm >= 11) {
        _mh = _mh + 1;
        _mm = _mm - 11;
    }
    let _mi = 0;
    if (_mh >= 24) {
        _mi = 1;
    }
    var _data = [];
    let _iagm = key ? new Date(key).getHours() === 23 ? 2 : 1 : 3;
    for (let i = _mi; i < _iagm; i++) {
        /*日*/
        var obj = {};
        obj.value = new Date(myDate.getTime() + (i * 24) * 60 * 60 * 1000).format("yyyy-MM-dd");
        obj.label = formatname(new Date(myDate.getTime() + (i * 24) * 60 * 60 * 1000).format("yyyy-MM-dd"));
        // $datename
        var _data2 = [];
        for (let n = 0; n < 24; n++) {
            /*点*/
            if (i != 0 || n >= _mh) {
                var obj2 = {};
                if (n < 10) {
                    obj2.label = '0' + (n) + '\u70b9';
                    obj2.value = '0' + (n);
                } else {
                    obj2.label = (n) + '\u70b9';
                    obj2.value = (n);
                }
                var _data3 = [];
                for (let y = 0; y < 12; y++) {
                    /*分*/
                    if (n != _mh || y >= _mm) {
                        var obj3 = {};
                        if (y == 0 || y == 1) {
                            obj3.label = '0' + (y * 5) + '\u5206';
                            obj3.value = '0' + (y * 5);
                        } else {
                            obj3.label = (y * 5) + '\u5206';
                            obj3.value = (y * 5);
                        }
                        _data3.push(obj3)
                    }
                }
                obj2.children = _data3;
                _data2.push(obj2);
            }
        }
        obj.children = _data2;
        _data.push(obj)
    }
    return _data;
}


