window.ysdt = {
    origin_center() {
        if (localStorage.getItem('origin_lng') && localStorage.getItem('origin_lat')) {
            return [localStorage.getItem('origin_lng'), localStorage.getItem('origin_lat')];
        }
        return [110.30083, 21.15122];
    },
    info: {
        destination: 'amap-destination',
        origin: 'amap-origin'
    },
    install_js(name, src) {
        return new Promise(function(resolve, reject) {
            if (document.getElementById(name)) { // 只加载一次
                resolve('ok')
                return
            }
            let script = document.createElement("script");
            script.type = "text/javascript";
            script.async = true;
            script.src = src;
            script.id = name;
            script.onerror = reject;
            script.onload = function(su) {
                resolve('ok')
            };
            document.head.appendChild(script);
        });
    },
    install_start() {
        return new Promise(function(resolve, reject) {
            window.ysdt.install_js('amap-demoutils', '//a.amap.com/jsapi_demos/static/demo-center/js/demoutils.js').then((e) => {
                window.ysdt.install_js('amap-demoutils2', '//webapi.amap.com/maps?v=1.4.6&key=160cab8ad6c50752175d76e61ef92c50&plugin=AMap.Driving,AMap.Geocoder,AMap.ToolBar').then((e) => {
                    window.ysdt.install_js('amap-demoutils3', 'http://git.ecgam.com/assets/amap/main.js').then((e) => {
                        resolve('ok')
                        return
                    });
                });
            })
        });
    },
    //展示路线规划
    showdriving(origin, destination) {
        this.install_start().then((e) => {
            var map = new AMap.Map('container', {
                zoom: 10,
                center: origin,
                scrollWheel: false,
                resizeEnable: true
            });
            new AMap.Driving({
                map: map,
                //panel: 'navResult'
            }).search(origin, destination);
        })
    },
    //获取展示路线规划
    getdriving(origin, destination) {
        this.install_start().then((e) => {
            var map = new AMap.Map('container', {
                zoom: 10,
                center: origin,
                scrollWheel: false,
                resizeEnable: true
            });
            new AMap.Driving({
                map: map,
                //panel: 'navResult'
            }).search(origin, destination, function(status, result) {
                console.log(result);
                let routes = result.routes[0];
                let drivingdata = {
                    origin: result.origin.lng + ',' + result.origin.lat,
                    origin_lat: result.origin.lat,
                    origin_lng: result.origin.lng,
                    destination: result.destination.lng + ',' + result.destination.lat,
                    destination_lat: result.destination.lat,
                    destination_lng: result.destination.lng,
                    distance: routes.distance,
                    duration: routes.duration,
                    tolls: routes.tolls,
                    price: (routes.distance * 0.001).toFixed(1),
                    aboutminutes: (routes.time / 60).toFixed(0),
                }
                ysdt._this.getprice(drivingdata);
                //alert(result.info);
            });
        })
    },
    //获取AMap
    amapinstall() {
        return new Promise(function(resolve, reject) {
            if (window.map) {
                resolve('ok');
                return
            }
            window.ysdt.install_start().then((e) => {
                window.map = new AMap.Map('container', {
                    zoom: 18,
                    center: window.ysdt.origin_center(),
                    scrollWheel: false,
                    resizeEnable: true
                });
                resolve('ok');
                return
            });
        });
    },
    //搜索出发
    getsearch(city, keyword) {
        return new Promise(function(resolve, reject) {
            window.ysdt.amapinstall().then((e) => {
                AMap.plugin('AMap.PlaceSearch', function() {
                    new AMap.PlaceSearch({ pageSize: 20, city: city }).search(keyword, function(status, result) {
                        // 搜索成功时，result即是对应的匹配数据
                        resolve(window.ysdt.getlistdata(result));
                    })
                })
            });
        });
    },
    //搜索数据处理
    getlistdata(result) {
        if (result.info === "OK") {
            let data = [];
            //console.log(result.poiList.pois);
            for (let x in result.poiList.pois) {
                data[x] = this.setpois(result.poiList.pois[x]);
            }
            return data;
        }
        return [];
    },
    setpois(pois) {
        return {
            name: pois.name,
            lat: pois.location.lat,
            lng: pois.location.lng,
            address: pois.address,
        }
    },
    setcenter(location) {
        map.setZoomAndCenter(18, location);
    },
    geolocation(_this) {
        window.ysdt.amapinstall().then((e) => {
            AMap.plugin('AMap.Geolocation', function() {
                var geolocation = new AMap.Geolocation({
                    enableHighAccuracy: true, //是否使用高精度定位，默认:true
                    timeout: 10000, //超过10秒后停止定位，默认：5s
                    buttonPosition: 'RB', //定位按钮的停靠位置
                    buttonOffset: new AMap.Pixel(10, 200), //定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
                    zoomToAccuracy: true, //定位成功后是否自动调整地图视野到定位点
                });
                map.addControl(geolocation);
                geolocation.getCurrentPosition(function(status, result) {
                    if (status == 'complete') {
                        if (_this.data.PositionPicker === true) {
                            map.setZoomAndCenter(18, result.position);
                        } else {
                            let data = {
                                name: result.formattedAddress.replace(result.formattedAddress.province, '').replace(result.formattedAddress.city, '').replace(result.formattedAddress.district, ''),
                                lat: result.position.lat,
                                lng: result.position.lng,
                                address: result.formattedAddress,
                            }
                            _this.getgeolocation(data);
                        }
                    } else {
                        _this.nogeolocation();
                    }
                });
            });

        });
    },
    PositionPicker(_this) {
        window.ysdt.amapinstall().then((e) => {
            window.map = new AMap.Map('container', {
                zoom: 18,
                center: window.ysdt.origin_center(),
                scrollWheel: false,
                resizeEnable: true
            });
            this.geolocation(_this);
            AMapUI.loadUI(['misc/PositionPicker', 'overlay/SimpleInfoWindow'], function(PositionPicker, SimpleInfoWindow) {
                new PositionPicker({
                    mode: 'dragMap',
                    map: map,
                    iconStyle: {
                        url: 'https://webapi.amap.com/ui/1.0/assets/position-picker2.png',
                        size: [48, 48],
                        ancher: [24, 40]
                    }
                }).on('success', function(positionResult) {
                    if (positionResult.regeocode.pois[0]) {
                        let data = window.ysdt.setpois(positionResult.regeocode.pois[0]);
                        data.lat = positionResult.position.lat;
                        data.lng = positionResult.position.lng;
                        _this.getnamedata(data);
                    }
                    // var infoWindow = new SimpleInfoWindow({
                    //     // infoTitle: '<strong>这里是标题</strong>',
                    //     infoBody: 'd'
                    // });
                    // infoWindow.open(map, map.getCenter());
                    //console.log(positionResult);

                }).on('fail', function(positionResult) {
                    console.log(positionResult);
                }).start();

            });
        });
    },
    dingwei(_this, id, lng, lat) {
        window.ysdt.install_start().then((e) => {
            window.map = new AMap.Map('container_' + id, {
                zoom: 18,
                center: [lng, lat],
                scrollWheel: false,
                resizeEnable: true
            });
            var marker = new AMap.Marker({
                position: [lng, lat], // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
                title: '北京'
            });
            map.add(marker);
        });
    },
}


