<template>
    <div :class="set.layout_class">
      <com-form  :data="set.form.data" :form="setdata()" @onsubmit="onsubmit" @getinfo="getinfo"/>
      <div :class="set.submit_class">
          <div v-if="set.form.accept" class="d-grid pt-2">
                <button class=" btn btn-primary" @click="exsubmit">确定</button>
          </div>
      </div>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      form:{},
    }
  },
  methods:{
      getinfo(key){
        this.form=key;
      },
      setdata(){
        let data={};
        for(let x in this.set.form.names){
          data[this.set.form.names[x]]=this.data[this.set.form.type][this.set.form.names[x]];
        }
       return data;
      },
      onvalidate(){
          for(let x in this.form){
            if(this.form[x]==="" || this.form[x].length===0){
              if(this.set.form[x]){
                if(this.set.form[x].validate){
                this.set.form[x].isActive=true
                return 0;
                }
              }
            }
          }
          return 1;
      },
      exsubmit(){
        if(this.onvalidate()===1){
         this.$emit('onsubmit',{method: 'post',url:this.set.form.accept,data:this.form,});
        }
      },
      onsubmit(key){
        this.$emit('onsubmit',key);
      }
  }
}
</script>

<style>

</style>


