<template>
    <div v-if="info[binding][show.state]">
      <div class="fs-5 fw-bold" v-if="info[binding][show.state][0]" v-html="info[binding][show.state][0]"></div>
      <div class="text-black-50 p-1" v-if="info[binding][show.state][1]" v-html="info[binding][show.state][1]"></div>
      <div class="alert alert-secondary  p-2" v-if="info[binding][show.state][2]" v-html="info[binding][show.state][2]"></div>
    </div>
</template>

<script>
export default {
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  },
  data(){
    return {
      info:{
        "p":{
          "71":["行程已取消","车主取消"],
          "72":["行程已取消","车主取消","因对方原因取消,已扣除对方的信任分和取消次数"],
          "81":["行程已取消","您主动取消,无责取消"],
          "82":["行程已取消","您主动取消","因你临时取消行程,已扣除你的信任分和取消次数"],
          "83":["行程已取消","行程未被接单,已取消"],
          "91":["行程已取消","超过出发时间未被接单,自动取消"],
        },
        "d":{
          "71":["行程已取消","您主动取消"],
          "72":["行程已取消","您主动取消","因你临时取消行程,已扣除你的信任分和取消次数"],
          "81":["行程已取消","乘客取消,无责取消"],
          "82":["行程已取消","乘客取消","因对方原因取消,已扣除对方的信任分和取消次数"],
          "91":["行程已取消","超过出发时间未被接单,自动取消"],
        },
      }
    }
  }
}
</script>

<style>

</style>


