<template>
    <div class="row align-items-center bg-white" @click="$emit('click')">
        <div class="col-auto">
            <div class="py-1">
                <span class="bi fs-5 text-black-50" :class="'bi-'+(data.icons?data.icons:'clock')"></span>   
            </div>
        </div>
        <div class="col p-0 border-bottom ">
            <div class="d-flex justify-content-between align-items-center">
                <div class="bd-highlight py-1">
                    <div>{{data.name}}</div>
                    <div v-if="data.address" class="text-black-50 fs-normal"><span v-if="data.adname">{{data.adname}}-</span>{{data.address}}</div>
                </div>
                <div class="bd-highlight">
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    data:{},
  },
}
</script>



