<template>
  <div>
    <div class="d-flex justify-content-between align-items-center p-2 border border-bottom border-light"  @click="$tourl(data.url)">
        <div class="bd-highlight fs-6">
            {{data.name}}
        </div>
        <div class="bd-highlight">
            {{form[name]}}
            <span class="bi bi-chevron-right"></span>
        </div>
    </div>
   </div>  
</template>

<script>
export default {
  props: {
    name:"",
    data:{},
    form:{},
  }
}
</script>



