<template>
    <div class="container-fluid">
        <div :class="layout">
            <Inner v-for="(item,index) in data" :key="index" :data="item" :type="type" :set="set" @click="getinfo(item)"></Inner>
        </div>
    </div>
</template>

<script>
import Inner from './inner/start.js'
import Empty from './empty/start'
export default {
    components:{
        Inner,
        Empty,
    },
    props: {
        layout:'',
        type:'',
        set:{},
        data:{}
    },
    methods:{
        getinfo(key){
            this.$emit('getinfo',key);
        },
    }
}
</script>



