<template>
    <div>
        <div class="p-3"><div class="fs-6">{{name==='enddate'?'最晚出发时间':'最早出发时间'}}</div></div>
        <div class="ys-eldate">
            <VuePicker :data="pickData"
                :layer="3"
                :defaultIndex="defaultIndex"
                :maskClick="false"
                :showToolbar="false"
                @change="change"
                :visible="true"
                />
        </div>
        <div class="container-fluid p-2">
            <div class="row gx-2" v-if="name==='enddate'">
                <div class="col d-grid">
                    <button class=" btn btn-white border text-primary" @click="onstart">上一步</button>
                </div>
                <div class="col d-grid">
                    <button class=" btn btn-primary" @click="onsubmit">确定</button>
                </div>
            </div>
            <div class="d-grid" v-else>
                <button class=" btn btn-primary" @click="onnext">下一步</button>
            </div>
        </div>
    </div>
</template>

<script>
import VuePicker from 'vue-pickers'
import {datardf} from '@/router/pickers-data'
export default {
  components: {
      VuePicker
  },
  props: {
    form:{},
  },
  data () {
    return {
        name:"startdate",
    defaultIndex: [],
    pickData:[],
    earlydate:'',
    }
  },
  created(){
       this.oninstall(this.form.startdate);
  },
    methods: {
        change(res){
            if(res && res[2]){
                this.data2={
                    label:res[0].label+' '+res[1].label+':'+res[2].label,
                    value:res[0].value+' '+res[1].value+':'+res[2].value,
                }
                this.form[this.name]=res[0].value+' '+res[1].value+':'+res[2].value;
            }
        },
        onstart(){
            this.name='startdate';
            this.oninstall(this.form.startdate);
        },
        onsubmit(){
            if(this.form.startdate && this.form.enddate){
               this.name='startdate';
               this.oninstall(this.form.startdate);
               this.$emit('onsubmit',this.form);
            }
        },
        onnext(){
            this.name='enddate';
            this.oninstall(this.form.enddate,this.form.startdate);
        },
        oninstall(key="",early=""){
            let arr=datardf(early);
            let _wz=[];
            for(let x in arr){
                if(arr[x].value===new Date(new Date(key).getTime() + 0 * 60 * 60 * 1000).format("yyyy-MM-dd")){
                    _wz[0]=x*1;
                    for(let e in arr[x].children){
                        if((arr[x].children[e].value*1)===new Date(key).getHours()){
                            _wz[1]=e*1;
                            for(let c in arr[x].children[e].children){
                                if((arr[x].children[e].children[c].value*1)===new Date(key).getMinutes()){
                                    _wz[2]=c*1;
                                }
                            }
                        }
                    }
                }
            }
            this.pickerVisible=true;
            this.defaultIndex=_wz;
            this.pickData=arr;
        },
    }
}
</script>
<style>
.ys-eldate .pickerbox[data-v-200a1302]{
    position:static;
    height:auto;
    z-index:auto;
}
.ys-eldate .vue-picker[data-v-200a1302] {
     position:static;
     z-index:auto;
}
</style>


