<template>
    <div class="text-center" v-if="info[binding][show.state]">
      <div class="fs-5 fw-bold" v-if="info[binding][show.state][0]">
        <span v-html="info[binding][show.state][0]"></span>
        <span v-if="show.ispay==='1'" class='fs-6 text-primary'>已支付</span>
        <span v-else-if="show.ispay==='2'" class='fs-6 text-primary'>您已确认收款</span>
        <span v-else-if="show.ispay==='3'" class='fs-6 text-primary'>平台已收取</span>
      </div>
      <div class="text-black-50 fs-sm p-1" v-if="info[binding][show.state][1]" v-html="info[binding][show.state][1]"></div>
    </div>
</template>

<script>
export default {
  props: {
    binding:'',
    data:{},
    show:{},
  },
  data(){
    return {
      info:{
        "p":{
          "22":["已确认同行","请如约出行，有责取消将影响取消次数和信任分"],
          "31":["司机已到达起点","请如约出行，有责取消将影响取消次数和信任分"],
          "32":["已确认到达起点","请如约出行，有责取消将影响取消次数和信任分"],
          "33":["司乘到达起点","请如约出行，有责取消将影响取消次数和信任分"],
          "41":["司机已确认上车",""],
          "42":["已确认上车",""],
          "43":["行程已开始",""],
          "51":["司机已确认到达目的地",""],
          "52":["已确认到达目的地",""],
          "53":["行程已结束",""],
        },
        "d":{
          "22":["乘客已确认同行","请如约出行，有责取消将影响取消次数和信任分"],
          "31":["已确认到达乘客起点","请如约出行，有责取消将影响取消次数和信任分"],
          "32":["乘客到达起点","请如约出行，有责取消将影响取消次数和信任分"],
          "33":["司乘到达起点","请如约出行，有责取消将影响取消次数和信任分"],
          "41":["已确认上车",""],
          "42":["乘客已确认上车",""],
          "43":["行程已开始",""],
          "51":["已确认到达目的地",""],
          "52":["乘客已确认到达目的地",""],
          "53":["行程已结束",""],
        }
      }
    }
  }
}
</script>

<style>

</style>


