<template>
  <div class="border rounded p-2 text-center" @click="$emit('click')">
    {{data.name}}<span v-if="data.number"><span class="text-primary" >{{data.number}}</span>{{data.unit}}</span>
  </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
  },
}
</script>



